import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let ii = Array(7);
	let ans = [];

	for (let i=0; i<ii.length; i++) {
		ii[i] = random(5)+1; if (random(2)===0) ii[i] = -ii[i];
	}

	if (language===ENGLISH) eq = "\\text{The real part of }"; else if (language===CHINESE) eq = "";
	if (ii[0]<-1) eq += ii[0]+"i"; else if (ii[0]===-1) eq += "-i"; else if (ii[0]===1) eq += "i"; if (ii[0]>1) eq += ii[0]+"i";
	for (let i=1; i<ii.length; i++) {
		if (ii[i]<-1) eq += ii[i]+"i^"+(i+1); else if (ii[i]===-1) eq += "-i^"+(i+1); else if (ii[i]===1) eq += "+i^"+(i+1); if (ii[i]>1) eq += "+"+ii[i]+"i^"+(i+1);
	}
	if (language===ENGLISH) eq += "\\text{ is}"; else if (language===CHINESE) eq += "\\text{ 的實部為}";

	ans[0] = -ii[1]+ii[3]-ii[5];
	ans[1] = ii[1]+ii[3]+ii[5];
	while (ans[1]===ans[0]) {ans[1] = random(10)+1; if (random(2)===0) ans[1] = -ans[1];}
	ans[2] = -ii[1]-ii[3]-ii[5];
	while (ans[2]===ans[0] || ans[2]===ans[1]) {ans[2] = random(10)+1; if (random(2)===0) ans[2] = -ans[2];}
	ans[3] = +ii[1]-ii[3]+ii[5];
	while (ans[3]===ans[0] || ans[3]===ans[1] || ans[3]===ans[2]) {ans[3] = random(10)+1; if (random(2)===0) ans[3] = -ans[3];}

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType26 = {
	generate,
};
export default MathQuestionGeneratorType26;
export { MathQuestionGeneratorType26 };
