import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let x1, x2, c1, c2, c3, c4, rhs1, rhs2;
	let sign1, sign2;

	do {
		do{
			c1 = random(20)+1; if (random(2)===0) c1 = -c1;
			c2 = random(20)+1; if (random(2)===0) c2 = -c2;
		} while (Math.abs(c2-c1)===1 || Math.abs(c2-c1)===11 || Math.abs(c2-c1)===13 || Math.abs(c2-c1)===17 || Math.abs(c2-c1)===19 || Math.abs(c2-c1)===23 || Math.abs(c2-c1)===29 || Math.abs(c2-c1)===31 || Math.abs(c2-c1)===37);
		do {x1 = random(9)+2; if (random(2)===0) x1 = -x1;} while ((c2-c1)%x1!==0);
		do {
			c3 = random(20)+1; if (random(2)===0) c3 = -c3;
			c4 = random(20)+1; if (random(2)===0) c4 = -c4;
		} while (Math.abs(c4-c3)===1 || Math.abs(c4-c3)===11 || Math.abs(c4-c3)===13 || Math.abs(c4-c3)===17 || Math.abs(c4-c3)===19 || Math.abs(c4-c3)===23 || Math.abs(c4-c3)===29 || Math.abs(c4-c3)===31 || Math.abs(c4-c3)===37);
		do {x2 = random(9)+2; if (random(2)===0) x2 = -x2;} while ((c3-c4)%x2!==0);
		sign1 = random(2)===0;
		sign2 = random(2)===0;
	} while ((c2-c1)*x2===(c4-c3)*x1);

	if (language===ENGLISH) eq = "\\text{The solution of}\\\\"; else if (language===CHINESE) eq = "";
	if (x1<-1) eq += x1+"x"; else if (x1===-1) eq += "-x"; else if (x1===1) eq += "x"; else if (x1>1) eq += x1+"x";
	if (c1<-1) eq += c1; else if (c1===-1) eq += "-1"; else if (c1===1) eq += "+1"; else if (c1>1) eq += "+"+c1;
	if (sign1) eq += "<"; else eq += ">";
	if (c2<-1) eq += c2; else if (c2===-1) eq += "-1"; else if (c2===1) eq += "1"; else if (c2>1) eq += c2;
	if (language===ENGLISH) eq += "\\text{ and }"; else if (language===CHINESE) eq += "\\text{ 和 }";
	if (x2<-1) eq += x2+"x"; else if (x2===-1) eq += "-x"; else if (x2===1) eq += "x"; else if (x2>1) eq += x2+"x";
	if (c3<-1) eq += c3; else if (c3===-1) eq += "-1"; else if (c3===1) eq += "+1"; else if (c3>1) eq += "+"+c3;
	if (sign2) eq += "<"; else eq += ">";
	if (c4<-1) eq += c4; else if (c4===-1) eq += "-1"; else if (c4===1) eq += "1"; else if (c4>1) eq += c4;
	if (language===ENGLISH) eq += "\\text{ is}"; else if (language===CHINESE) eq += "\\text{ 的解為}";

	rhs1 = (c2-c1)/x1; if (x1<0) sign1 = !sign1;
	rhs2 = (c4-c3)/x2; if (x2<0) sign2 = !sign2;

	if (sign1 && sign2) {
		if (rhs1>rhs2) {
			answers[0] = "x<"+rhs2;
			answers[1] = "x>"+rhs2;
			if (random(2)===0) answers[2] = "x<"+rhs1; else answers[2] = rhs2+"<x<"+rhs1;
			if (random(2)===0) answers[3] = "x>"+rhs1; else if (language===ENGLISH) answers[3] = "\\text{no solution.}"; else if (language===CHINESE) answers[3] = "\\text{沒解}";
		} else {
			answers[0] = "x<"+rhs1;
			answers[1] = "x>"+rhs1;
			if (random(2)===0) answers[2] = "x<"+rhs2; else answers[2] = rhs1+"<x<"+rhs2;
			if (random(2)===0) answers[3] = "x>"+rhs2; else if (language===ENGLISH) answers[3] = "\\text{no solution.}"; else if (language===CHINESE) answers[3] = "\\text{沒解}";
		}
	} else if (!sign1 && !sign2) {
		if (rhs1>rhs2) {
			answers[0] = "x>"+rhs1;
			answers[1] = "x<"+rhs1;
			if (random(2)===0) answers[2] = "x>"+rhs2; else answers[2] = rhs2+"<x<"+rhs1;
			if (random(2)===0) answers[3] = "x<"+rhs2; else if (language===ENGLISH) answers[3] = "\\text{no solution}"; else if (language===CHINESE) answers[3] = "\\text{沒解}";
		} else {
			answers[0] = "x>"+rhs2;
			answers[1] = "x<"+rhs2;
			if (random(2)===0) answers[2] = "x>"+rhs1; else answers[2] = rhs1+"<x<"+rhs2;
			if (random(2)===0) answers[3] = "x<"+rhs1; else if (language===ENGLISH) answers[3] = "\\text{no solution}"; else if (language===CHINESE) answers[3] = "\\text{沒解}";
		}
	} else if ((sign1 && !sign2 && rhs1<rhs2) || (!sign1 && sign2 && rhs1>rhs2)) {
		if (language===ENGLISH) answers[0] = "\\text{no solution}"; else if (language===CHINESE) answers[0] = "\\text{沒解}";
		answers[1] = "x<"+rhs1;
		if (random(2)===0) answers[2] = "x>"+rhs1; else answers[2] = "x<"+rhs2;
		answers[3] = "x>"+rhs2;
	} else if (sign1 && !sign2 && rhs1>rhs2) {
		answers[0] = rhs2+"<x<"+rhs1;
		answers[1] = rhs2+"<x";
		answers[2] = "x<"+rhs1;
		if (language===ENGLISH) answers[3] = "\\text{no solution}"; else if (language===CHINESE) answers[3] = "\\text{沒解}";
	} else if (!sign1 && sign2 && rhs1<rhs2) {
		answers[0] = rhs1+"<x<"+rhs2;
		answers[1] = rhs1+"<x";
		answers[2] = "x<"+rhs2;
		if (language===ENGLISH) answers[3] = "\\text{no solution}"; else if (language===CHINESE) answers[3] = "\\text{沒解}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType21 = {
	generate,
};
export default MathQuestionGeneratorType21;
export { MathQuestionGeneratorType21 };
