import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let colour = 0;
	let ball = Array(2).fill(0).map(() =>  Array(4).fill(0));
	let ans_x = [];
	let ans_y1 = 0;
	let ans_y2 = 0;

	for (let i=0; i<ball.length; i++) {
		do {
			colour = 0;
			for (let j=0; j<ball[i].length; j++) {
				if (random(2)===0) {
					ball[i][j] = random(7)+2;
					colour++;
				} else {
					ball[i][j] = 0;
				}
			}
		} while (colour!==3);
	}

	if (language===ENGLISH) eq = "\\text{Balls in }1^{st}\\text{ bag: "; else if (language===CHINESE) eq = "\\text{袋一有球：";
	colour = 0;
	for (let i=0; i<4; i++) {
		if (ball[0][i]>0) {
			if (colour>0) {if (language===ENGLISH) eq += ", "; else if (language===CHINESE) eq += "，";}
			eq += ball[0][i];
			switch (i) {
			case 0:
				if (language===ENGLISH) eq += " red"; else if (language===CHINESE) eq += " 紅";
				break;
			case 1:
				if (language===ENGLISH) eq += " yellow"; else if (language===CHINESE) eq += " 黃";
				break;
			case 2:
				if (language===ENGLISH) eq += " blue"; else if (language===CHINESE) eq += " 藍";
				break;
			case 3:
				if (language===ENGLISH) eq += " green"; else if (language===CHINESE) eq += " 綠";
				break;
			default:
				break;
			}
			colour++;
		}
	}
	if (language===ENGLISH) eq += "}\\\\\\text{Balls in }2^{nd}\\text{ bag: "; else if (language===CHINESE) eq += "}\\\\\\text{袋二有球：";
	colour = 0;
	for (let i=0; i<4; i++) {
		if (ball[1][i]>0) {
			if (colour>0) {if (language===ENGLISH) eq += ", "; else if (language===CHINESE) eq += "，";}
			eq += ball[1][i];
			switch (i) {
			case 0:
				if (language===ENGLISH) eq += " red"; else if (language===CHINESE) eq += " 紅";
				break;
			case 1:
				if (language===ENGLISH) eq += " yellow"; else if (language===CHINESE) eq += " 黃";
				break;
			case 2:
				if (language===ENGLISH) eq += " blue"; else if (language===CHINESE) eq += " 藍";
				break;
			case 3:
				if (language===ENGLISH) eq += " green"; else if (language===CHINESE) eq += " 綠";
				break;
			default:
				break;
			}
			colour++;
		}
	}
	if (language===ENGLISH) eq += "}\\\\\\text{Draw one ball from each bag,}\\\\\\text{P(two balls are of different colours)}="; else if (language===CHINESE) eq += "}\\\\\\text{若從各個袋隨機抽取一個球，}\\\\\\text{兩個球顏色不相同的機會率}=";

	ans_y1 = 0;
	for (let i=0; i<ball[0].length; i++) {
		ans_y1 += ball[0][i];
	}
	ans_y2 = 0;
	for (let i=0; i<ball[1].length; i++) {
		ans_y2 += ball[1][i];
	}
	ans_x[0] = 0;
	for (let i=0; i<4; i++) {
		ans_x[0] += ball[0][i]*ball[1][i];
	}

	ans_x[0] = ans_y1*ans_y2-ans_x[0];
	do {
		ans_x[1] = ans_x[0]; if (random(2)===0) ans_x[1] += random(10)+1; else ans_x[1] -= random(10)+1;
	} while (ans_x[1]===ans_x[0] || ans_x[1]<=0 || ans_x[1]>=ans_y1*ans_y2);
	do {
		ans_x[2] = ans_x[1]; if (random(2)===0) ans_x[2] += random(10)+1; else ans_x[2] -= random(10)+1;
	} while (ans_x[2]===ans_x[0] || ans_x[2]===ans_x[1] || ans_x[2]<=0 || ans_x[2]>=ans_y1*ans_y2);
	do {
		ans_x[3] = ans_x[2]; if (random(2)===0) ans_x[3] += random(10)+1; else ans_x[3] -= random(10)+1;
	} while (ans_x[3]===ans_x[0] || ans_x[3]===ans_x[1] || ans_x[3]===ans_x[2] || ans_x[3]<=0 || ans_x[2]>=ans_y1*ans_y2);

	for (let i=0; i<answers.length; i++) {
		answers[i] = "\\frac{"+ans_x[i]+"}{"+(ans_y1*ans_y2)+"}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType11 = {
	generate,
};
export default MathQuestionGeneratorType11;
export { MathQuestionGeneratorType11 };
