import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let angle1 = 0;
	let angle2 = 0;
	let angle3 = 0;
	let q_side = 0;
	let ans_n = [];
	let ans_d = [];
	let angle = Array(3);

	q_side = random(2)+1;
	do {
		for (let i=0; i<angle.length; i++) {
			let temp = random(3);
			switch (temp) {
			case 0:
				angle[i] = 'A';
				break;
			case 1:
				angle[i] = 'B';
				break;
			case 2:
				angle[i] = 'C';
				break;
			default:
				break;
			}
		}
	} while (angle[0]===angle[1] || angle[0]===angle[2] || angle[1]===angle[2]);

	do {
		angle1 = random(16)+60;
		angle2 = random(16)+60;
		angle3 = 180-angle1-angle2;
	} while (angle1===angle2 || angle1===angle3 || angle2===angle3);

	if (language===ENGLISH) eq = "\\text{In }"; else if (language===CHINESE) eq = "\\text{在 }";
	eq += "\\triangle{ABC}\\text{, }\\angle{"+angle[0]+"}="+angle1+"°\\text{, }\\angle{"+angle[1]+"}="+angle2+"°\\\\"+angle[0]+angle[1]+"=";

	if (q_side===1) {
		ans_n[0] = angle3; ans_d[0] = angle2;
		ans_n[1] = angle1; ans_d[1] = angle2;
		ans_n[2] = angle3; ans_d[2] = angle1;
		ans_n[3] = angle2; ans_d[3] = angle3;

		for (let i=0; i<answers.length; i++) {
			answers[i] = "\\frac{"+angle[0]+angle[2]+"\\times\\text{sin}\\angle{"+ans_n[i]+"°}}{\\text{sin}\\angle{"+ans_d[i]+"°}}";
		}
	} else if (q_side===2) {
		ans_n[0] = angle3; ans_d[0] = angle1;
		ans_n[1] = angle2; ans_d[1] = angle1;
		ans_n[2] = angle3; ans_d[2] = angle2;
		ans_n[3] = angle1; ans_d[3] = angle3;

		for (let i=0; i<answers.length; i++) {
			answers[i] = "\\frac{"+angle[1]+angle[2]+"\\times\\text{sin}\\angle{"+ans_n[i]+"°}}{\\text{sin}\\angle{"+ans_d[i]+"°}}";
		}
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType12 = {
	generate,
};
export default MathQuestionGeneratorType12;
export { MathQuestionGeneratorType12 };
