import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let rd_m, rd_n, ratio_A, ratio_B, ratio_C, hypotenuse;
	let ans_n = [],
		ans_d = [];
	let angle_1, angle_2, angle_3;

	do {
		rd_m = random(7)+1;
		rd_n = random(7)+1;
	} while ((rd_m%2===0 && rd_n%2===0) || (rd_m%2===1 && rd_n%2===1) || rd_m<=rd_n);

	hypotenuse = random(3)+1;
	ratio_A = 0; ratio_B = 0; ratio_C = 0;
	switch (hypotenuse) {
	case 1:
		ratio_A = Math.trunc(Math.pow(rd_m, 2)+Math.pow(rd_n, 2));
		if (random(2)===0) {
			ratio_B = 2*rd_m*rd_n;
			ratio_C = Math.trunc(Math.pow(rd_m, 2)-Math.pow(rd_n, 2));
		} else {
			ratio_B = Math.trunc(Math.pow(rd_m, 2)-Math.pow(rd_n, 2));
			ratio_C = 2*rd_m*rd_n;
		}
		break;
	case 2:
		ratio_B = Math.trunc(Math.pow(rd_m, 2)+Math.pow(rd_n, 2));
		if (random(2)===0) {
			ratio_A = 2*rd_m*rd_n;
			ratio_C = Math.trunc(Math.pow(rd_m, 2)-Math.pow(rd_n, 2));
		} else {
			ratio_A = Math.trunc(Math.pow(rd_m, 2)-Math.pow(rd_n, 2));
			ratio_C = 2*rd_m*rd_n;
		}
		break;
	case 3:
		ratio_C = Math.trunc(Math.pow(rd_m, 2)+Math.pow(rd_n, 2));
		if (random(2)===0) {
			ratio_A = 2*rd_m*rd_n;
			ratio_B = Math.trunc(Math.pow(rd_m, 2)-Math.pow(rd_n, 2));
		} else {
			ratio_A = Math.trunc(Math.pow(rd_m, 2)-Math.pow(rd_n, 2));
			ratio_B = 2*rd_m*rd_n;
		}
		break;
	default:
		break;
	}

	let rd = random(4);
	angle_1 = 0; angle_2 = 0; angle_3 = 0;
	switch (rd) {
	case 0:
		angle_1 = 'A'; angle_2 = 'B'; angle_3 = 'C';
		break;
	case 1:
		angle_1 = 'D'; angle_2 = 'E'; angle_3 = 'F';
		break;
	case 2:
		angle_1 = 'P'; angle_2 = 'Q'; angle_3 = 'R';
		break;
	case 3:
		angle_1 = 'X'; angle_2 = 'Y'; angle_3 = 'Z';
		break;
	default:
		break;
	}

	if (language===ENGLISH) eq = "\\text{In }"; else if (language===CHINESE) eq = "\\text{在 }";
	eq += "\\triangle{"+angle_1+angle_2+angle_3+"}";
	if (language===ENGLISH) eq += "\\text{, }"; else if (language===CHINESE) eq += "\\text{，}";
	eq += ""+angle_1+angle_2+":"+angle_2+angle_3+":"+angle_1+angle_3+"=\\\\"+ratio_A+":"+ratio_B+":"+ratio_C;
	if (language===ENGLISH) eq += "\\text{. cos}"; else if (language===CHINESE) eq += "\\text{。cos}";
	switch (hypotenuse) {
	case 1:
		eq += angle_1;
		break;
	case 2:
		eq += angle_2;
		break;
	case 3:
		eq += angle_1;
		break;
	default:
		break;
	}
	eq += ":\\text{cos}";
	switch (hypotenuse) {
	case 1:
		eq += angle_2;
		break;
	case 2:
		eq += angle_3;
		break;
	case 3:
		eq += angle_3;
		break;
	default:
		break;
	}
	eq += "=";

	switch (hypotenuse) {
	case 1:
		ans_n[0] = ratio_C; ans_d[0] = ratio_B;
		ans_n[1] = ratio_B; ans_d[1] = ratio_C;
		ans_n[2] = ratio_C; ans_d[2] = ratio_A;
		ans_n[3] = ratio_B; ans_d[3] = ratio_A;
		break;
	case 2:
		ans_n[0] = ratio_A; ans_d[0] = ratio_C;
		ans_n[1] = ratio_C; ans_d[1] = ratio_A;
		ans_n[2] = ratio_A; ans_d[2] = ratio_B;
		ans_n[3] = ratio_C; ans_d[3] = ratio_B;
		break;
	case 3:
		ans_n[0] = ratio_A; ans_d[0] = ratio_B;
		ans_n[1] = ratio_B; ans_d[1] = ratio_A;
		ans_n[2] = ratio_A; ans_d[2] = ratio_C;
		ans_n[3] = ratio_B; ans_d[3] = ratio_C;
		break;
	default:
		break;
	}

	for (let i=0; i<answers.length; i++) {
		answers[i] = ans_n[i]+":"+ans_d[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType29 = {
	generate,
};
export default MathQuestionGeneratorType29;
export { MathQuestionGeneratorType29 };
