import { random } from "../random";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let one = 0;
	let temp = 0;
	let bin = [];
	let ans_cx = [];
	let ans_cy = [];
	let ans_sum = [];

	bin = Array(random(4)+12);

	do {
		one = 0;
		bin[bin.length-1] = 1;
		for (let i=bin.length-2; i>=0; i--) {
			bin[i] = random(2);
			if (bin[i]===1) {
				one++;
			}
		}
	} while (one!==4);

	eq = "";
	for (let i=bin.length-1; i>=0; i--) {
		eq += bin[i];
	}
	eq += "_{2}=";

	ans_cx[0] = bin.length-1;
	ans_cy[0] = bin.length-2;
	while (bin[ans_cy[0]]!==1) {
		ans_cy[0]--;
	}

	temp = ans_cy[0]-1;
	ans_sum[0] = 0;
	while (temp>=0) {
		if (bin[temp]===1) {
			ans_sum[0] += Math.pow(2, temp);
		}
		temp--;
	}

	ans_cx[1] = ans_cx[0]+1;
	ans_cy[1] = ans_cy[0]+1;
	ans_sum[1] = ans_sum[0];

	ans_cx[2] = ans_cx[0]+1;
	ans_cy[2] = ans_cy[0]+1;
	ans_sum[2] = ans_sum[0]*2;

	ans_cx[3] = ans_cx[0];
	ans_cy[3] = ans_cy[0];
	ans_sum[3] = ans_sum[0]*2;

	for (let i=0; i<answers.length; i++) {
		answers[i] = "2^{"+ans_cx[i]+"}+2^{"+ans_cy[i]+"}+"+ans_sum[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType7 = {
	generate,
};
export default MathQuestionGeneratorType7;
export { MathQuestionGeneratorType7 };
