import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let x1, x2, k1, k2, kk;
	let ans1 = [];
	let ans2 = [];

	do {
		x1 = random(8)+1; if (random(2)===0) x1 = -x1;
		x2 = random(8)+1; if (random(2)===0) x2 = -x2;
		k1 = random(8)+1; if (random(2)===0) k1 = -k1;
		k2 = random(8)+1; if (random(2)===0) k2 = -k2;
		kk = random(8)+1; if (random(2)===0) kk = -kk;
	} while (!(Math.pow(x1*k2+x2*k1, 2)-4*x1*x2*(k1*k2-kk)>=0 && ((-(x1*k2+x2*k1)+Math.sqrt(Math.pow(x1*k2+x2*k1, 2)-4*x1*x2*(k1*k2-kk)))/(2*x1*x2))%1===0 && ((-(x1*k2+x2*k1)-Math.sqrt(Math.pow(x1*k2+x2*k1, 2)-4*x1*x2*(k1*k2-kk)))/(2*x1*x2))%1===0));

	if (language===ENGLISH) eq = "\\text{Let k be a constant. Solve:}\\\\("; else if (language===CHINESE) eq = "\\text{設 k 為一常數，試解：}\\\\(";
	if (x1<-1) eq += x1+"x"; else if (x1===-1) eq += "-x"; else if (x1===1) eq += "x"; else if (x1>1) eq += x1+"x";
	if (k1<-1) eq += k1+"k"; else if (k1===-1) eq += "-k"; else if (k1===1) eq += "+k"; else if (k1>1) eq += "+"+k1+"k";
	eq += ")(";
	if (x2<-1) eq += x2+"x"; else if (x2===-1) eq += "-x"; else if (x2===1) eq += "x"; else if (x2>1) eq += x2+"x";
	if (k2<-1) eq += k2+"k"; else if (k2===-1) eq += "-k"; else if (k2===1) eq += "+k"; else if (k2>1) eq += "+"+k2+"k";
	eq += ")=";
	if (kk<-1) eq += kk+"k^2"; else if (kk===-1) eq += "-k^2"; else if (kk===1) eq += "k^2"; else if (kk>1) eq += kk+"k^2";

	ans1[0] = Math.trunc((-(x1*k2+x2*k1)+Math.sqrt(Math.pow(x1*k2+x2*k1, 2)-4*x1*x2*(k1*k2-kk)))/(2*x1*x2));
	ans2[0] = Math.trunc((-(x1*k2+x2*k1)-Math.sqrt(Math.pow(x1*k2+x2*k1, 2)-4*x1*x2*(k1*k2-kk)))/(2*x1*x2));
	do {
		ans1[1] = random(5)+1; if (random(2)===0) ans1[1] = -ans1[1];
		ans2[1] = random(10)+1; if (random(2)===0) ans2[1] = -ans2[1]; else ans2[1] = 0;
	} while (ans1[1]===ans1[0] && ans2[1]===ans2[0]);
	do {
		ans1[2] = random(5)+1; if (random(2)===0) ans1[2] = -ans1[2];
		ans2[2] = random(5)+1; if (random(2)===0) ans2[2] = -ans2[2]; else ans2[2] = -ans1[2];
	} while ((ans1[2]===ans1[0] && ans2[2]===ans2[0]) || (ans1[2]===ans1[1] && ans2[2]===ans2[1]));
	do {
		ans1[3] = random(5)+1; if (random(2)===0) ans1[3] = -ans1[3];
		ans2[3] = random(5)+1; if (random(2)===0) ans2[3] = -ans2[3]; else ans2[3] = ans1[3];
	} while ((ans1[3]===ans1[0] && ans2[3]===ans2[0]) || (ans1[3]===ans1[1] && ans2[3]===ans2[1]) || (ans1[3]===ans1[2] && ans2[3]===ans2[2]));

	for (let i=0; i<answers.length; i++) {
		answers[i] = "x=";
		if (ans1[i]<-1) answers[i] += ans1[i]+"k"; else if (ans1[i]===-1) answers[i] += "-k"; else if (ans1[i]===0) answers[i] += "0"; else if (ans1[i]===1) answers[i] += "k"; else if (ans1[i]>1) answers[i] += ans1[i]+"k";
		if (ans2[i]!==ans1[i]) {
			answers[i] += "\\text{ or }x=";
			if (ans2[i]<-1) answers[i] += ans2[i]+"k"; else if (ans2[i]===-1) answers[i] += "-k"; else if (ans2[i]===0) answers[i] += "0"; else if (ans2[i]===1) answers[i] += "k"; else if (ans2[i]>1) answers[i] += ans2[i]+"k";
		}
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType23 = {
	generate,
};
export default MathQuestionGeneratorType23;
export { MathQuestionGeneratorType23 };
