import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let num, equal_type;
	let ans_n = Array(4).fill(0);
	let ans_d = [];

	equal_type = random(5)+1;

	num = 0;
	if (language===ENGLISH) {
		eq = "\\text{Two fair dice are thrown.}\\\\\\text{P(sum of two numbers";
		switch (equal_type) {
		case 1:
			num = random(7)+4;
			eq += "}≤"+num+"\\text{)}=";
			break;
		case 2:
			num = random(7)+4;
			eq += "}="+num+"\\text{)}=";
			break;
		case 3:
			num = random(7)+4;
			eq += "}≥"+num+"\\text{)}=";
			break;
		case 4:
			eq += " is a prime number)}=";
			break;
		case 5:
			eq += " is a composite number)}=";
			break;
		default:
			break;
		}
	} else if (language===CHINESE) {
		eq = "\\text{擲兩粒骰子，擲出的兩個數字總和";
		switch (equal_type) {
		case 1:
			num = random(7)+4;
			eq += "不大於 "+num+" 的機率}=";
			break;
		case 2:
			num = random(7)+4;
			eq += "等於 "+num+" 的機率}=";
			break;
		case 3:
			num = random(7)+4;
			eq += "不少於 "+num+" 的機率}=";
			break;
		case 4:
			eq += "為質數的機率}=";
			break;
		case 5:
			eq += "為合成數的機率}=";
			break;
		default:
			break;
		}
	}

	let sum = Array(6).fill(0).map(() =>  Array(6).fill(0));
	for (let i=0; i<sum.length; i++) {
		for (let j=0; j<sum[i].length; j++) {
			sum[i][j] = i+j+2;
		}
	}

	switch (equal_type) {
	case 1:
		for (let i=0; i<sum.length; i++) {
			for (let j=0; j<sum[i].length; j++) {
				if (sum[i][j]<=num) {
					ans_n[0] ++;
				}
			}
		}
		ans_d[0] = 36;
		break;
	case 2:
		for (let i=0; i<sum.length; i++) {
			for (let j=0; j<sum[i].length; j++) {
				if (sum[i][j]===num) {
					ans_n[0] ++;
				}
			}
		}
		ans_d[0] = 36;
		break;
	case 3:
		for (let i=0; i<sum.length; i++) {
			for (let j=0; j<sum[i].length; j++) {
				if (sum[i][j]>=num) {
					ans_n[0] ++;
				}
			}
		}
		ans_d[0] = 36;
		break;
	case 4:
		ans_n[0] = 15;
		ans_d[0] = 36;
		break;
	case 5:
		ans_n[0] = 21;
		ans_d[0] = 36;
		break;
	default:
		break;
	}

	do {
		ans_n[1] = random(25)+6;
		ans_d[1] = 36;
	} while (ans_n[1]===ans_n[0]);
	do {
		ans_n[2] = random(25)+6;
		ans_d[2] = 36;
	} while (ans_n[2]===ans_n[0] || ans_n[2]===ans_n[1]);
	do {
		ans_n[3] = random(25)+6;
		ans_d[3] = 36;
	} while (ans_n[3]===ans_n[0] || ans_n[3]===ans_n[1] || ans_n[3]===ans_n[2]);

	for (let i=0; i<4; i++) {
		for (let n=10; n>=2; n--) {
			if (ans_n[i]%n===0 && ans_d[i]%n===0) {
				ans_n[i] /= n;
				ans_d[i] /= n;
			}
		}
	}

	for (let i=0 ; i<answers.length; i++) {
		answers[i] = "\\frac{"+ans_n[i]+"}{"+ans_d[i]+"}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType31 = {
	generate,
};
export default MathQuestionGeneratorType31;
export { MathQuestionGeneratorType31 };
