import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let coef_x3 = 0;
	let coef_x2 = 0;
	let coef_x1 = 0;
	let coef_x0 = 0;
	let n = 0;
	let ans = [];

	do {
		coef_x3 = random(3)+1; if (random(2)===0) coef_x3 = -coef_x3;
		coef_x2 = random(5)+1; if ((coef_x2<0 && coef_x3>0) || random(2)===0) coef_x2 = -coef_x2;
		coef_x1 = random(5)+1; if ((coef_x1<0 && coef_x2>0) || random(2)===0) coef_x1 = -coef_x1;
		coef_x0 = random(3)+1; if ((coef_x0<0 && coef_x1>0) || random(2)===0) coef_x0 = -coef_x0;
		n = random(3)+1; if (random(2)===0) n = -n;
	} while (coef_x0-coef_x1*n+coef_x2*n*n-coef_x3*n*n*n===coef_x0+coef_x1*n+coef_x2*n*n+coef_x3*n*n*n || coef_x0-coef_x1*n+coef_x2*n*n-coef_x3*n*n*n===coef_x0 || coef_x0+coef_x1*n+coef_x2*n*n+coef_x3*n*n*n===coef_x0 || coef_x0-coef_x1*n+coef_x2*n*n-coef_x3*n*n*n===coef_x0+n);

	eq = "f(x) = ";
	if (coef_x3<-1) eq += coef_x3+"x^3"; else if (coef_x3===-1) eq += "-x^3"; else if (coef_x3===1) eq += "x^3"; else if (coef_x3>1) eq += coef_x3+"x^3";
	if (coef_x2<-1) eq += coef_x2+"x^2"; else if (coef_x2===-1) eq += "-x^2"; else if (coef_x2===1) eq += "+x^2"; else if (coef_x2>1) eq += "+"+coef_x2+"x^2";
	if (coef_x1<-1) eq += coef_x1+"x"; else if (coef_x1===-1) eq += "-x"; else if (coef_x1===1) eq += "+x"; else if (coef_x1>1) eq += "+"+coef_x1+"x";
	if (coef_x0<-1) eq += coef_x0; else if (coef_x0===-1) eq += "-1"; else if (coef_x0===1) eq += "+1"; else if (coef_x0>1) eq += "+"+coef_x0;
	if (language===ENGLISH) eq += "\\text{ divided by }x"; else if (language===CHINESE) eq += "\\text{ 除以 }x";
	if (n<-1) eq += n; else if (n===-1) eq += "-1"; else if (n===1) eq += "+1"; else if (n>1) eq += "+"+n;
	if (language===ENGLISH) eq += "\\\\\\text{Remainder}="; else if (language===CHINESE) eq += "\\\\\\text{餘數}=";


	ans[0] = coef_x0-coef_x1*n+coef_x2*n*n-coef_x3*n*n*n;
	ans[1] = coef_x0+coef_x1*n+coef_x2*n*n+coef_x3*n*n*n;
	ans[2] = coef_x0; if (random(2)===0) do {ans[2] = random(201)-100; } while (ans[2]===ans[0] || ans[2]===ans[1]);
	ans[3] = coef_x0+n; if (ans[3]===ans[2] || random(2)===0) do {ans[3] = random(201)-100; } while (ans[3]===ans[0] || ans[3]===ans[1] || ans[3]===ans[2]);

	answers[0] = ""+ans[0];
	answers[1] = ""+ans[1];
	answers[2] = ""+ans[2];
	answers[3] = ""+ans[3];

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType3 = {
	generate,
};
export default MathQuestionGeneratorType3;
export { MathQuestionGeneratorType3 };
