import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let map, scale_1, scale_2, map_unit, actual_unit;
	let ans = [];

	map = random(26)+15;
	scale_1 = random(6)+5;
	map_unit = random(2)+1;
	actual_unit = 0; if (map_unit===1) actual_unit = random(2)+2; else if (map_unit===2) actual_unit = 3;

	scale_2 = 0;
	if (map_unit===1) {
		if (actual_unit===2) {
			scale_2 = 100;
		} else if (actual_unit===3) {
			scale_2 = 100000;
		}
	} else if (map_unit===2) {
		if (actual_unit===3) {
			scale_2 = 1000;
		}
	}

	if (language===ENGLISH) eq = "\\text{The actual area of a place is }"; else if (language===CHINESE) eq = "\\text{某地方的實際面積為 }";
	eq += Math.trunc(map*Math.pow(scale_1, 2))+"\\text{";
	if (actual_unit===2) eq += "m"; else if (actual_unit===3) eq += "km";
	eq += "}^2\\text{";
	if (language===ENGLISH) eq += ". If its area on the map is }"; else if (language===CHINESE) eq += "。}\\\\\\text{若其地圖面積為 }";
	eq += map+"\\text{";
	if (map_unit===1) eq += "cm"; else if (map_unit===2) eq += "m";
	eq += "}^2";
	if (language===ENGLISH) eq += "\\text{, then the scale of the map is}"; else if (language===CHINESE) eq += "\\text{，}\\\\\\text{那地圖的比例是}";

	ans[0] = scale_1*scale_2;
	ans[1] = (map*Math.pow(scale_1, 2))/map;
	if (ans[1]===ans[0]) {
		if (random(2)===0) {
			ans[1] = ans[0]*10;
		} else {
			ans[1] = ans[0]*100;
		}
	}
	ans[2] = scale_1*Math.sqrt(scale_2);
	if (actual_unit!==2 || ans[2]===ans[0] || ans[2]===ans[1]) {
		if (random(2)===0) {
			ans[2] = ans[0]*100;
		} else {
			ans[2] = ans[0]*1000;
		}
	}
	ans[3] = Math.pow(ans[0], 2);
	if (ans[3]===ans[0] || ans[3]===ans[1] || ans[3]===ans[2]) {
		if (random(2)===0) {
			ans[3] = ans[0]*1000;
		} else {
			ans[3] = ans[0]*10000;
		}
	}

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType27 = {
	generate,
};
export default MathQuestionGeneratorType27;
export { MathQuestionGeneratorType27 };
