import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let largest;
	let range;
	let base = [];
	let index = [];
	let largest_result;
	let result = [];

	range = random(300)+201;

	do {
		base[0] = random(500)+1;
		index[0] = random(500)+1;
		result[0] = index[0]*Math.log10(base[0]);
	} while (Math.abs(result[0]-range)>50);

	do {
		do {
			base[1] = random(500)+1;
		} while (base[1]===base[0]);
		do {
			index[1] = random(500)+1;
		} while (index[1]===index[0]);
		result[1] = index[1]*Math.log10(base[1]);
	} while (Math.abs(result[1]-range)>50 || result[1]===result[0]);

	do {
		do {
			base[2] = random(500)+1;
		} while (base[2]===base[0] || base[2]===base[1]);
		do {
			index[2] = random(500)+1;
		} while (index[2]===index[0] || index[2]===index[1]);
		result[2] = index[2]*Math.log10(base[2]);
	} while (Math.abs(result[2]-range)>50 || result[2]===result[0] || result[2]===result[1]);

	do {
		do {
			base[3] = random(500)+1;
		} while (base[3]===base[0] || base[3]===base[1] || base[3]===base[2]);
		do {
			index[3] = random(500)+1;
		} while (index[3]===index[0] || index[3]===index[1] || index[3]===index[2]);
		result[3] = index[3]*Math.log10(base[3]);
	} while (Math.abs(result[3]-range)>50 || result[3]===result[0] || result[3]===result[1] || result[3]===result[2]);

	largest = -1;
	largest_result = 0;
	for (let i=0; i<result.length; i++) {
		if (result[i]>largest_result) {
			largest = i;
			largest_result = result[i];
		}
	}

	if (largest!==0) {
		let temp;
		temp = base[largest];
		base[largest] = base[0];
		base[0] = temp;

		temp = index[largest];
		index[largest] = index[0];
		index[0] = temp;
	}

	if (language===ENGLISH) eq = "\\text{Which of the followings is the largest?}"; else if (language===CHINESE) eq = "\\text{以下哪一個數最大？}";

	for (let i=0; i<answers.length; i++) {
		answers[i] = base[i]+"^{"+index[i]+"}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType18 = {
	generate,
};
export default MathQuestionGeneratorType18;
export { MathQuestionGeneratorType18 };
