import { random } from "../random";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let a1, b1, c1;
	let a2, b2, c2;
	let coef_a2, coef_b2, coef_ab, coef_a, coef_b, coef_c;
	let ans_a1 = [];
	let ans_a2 = [];
	let ans_b1 = [];
	let ans_b2 = [];
	let ans_c1 = [];
	let ans_c2 = [];

	a1 = random(3)+1; if (random(2)===0) a1 = -a1;
	b1 = random(3)+1; if (random(2)===0) b1 = -b1;
	c1 = random(3)+1; if (random(2)===0) c1 = -c1;

	a2 = random(3)+1; if (random(2)===0) a2 = -a2;
	b2 = random(3)+1; if (random(2)===0) b2 = -b2;
	c2 = random(3)+1; if (random(2)===0) c2 = -c2;

	coef_a2 = a1*a2;
	coef_b2 = b1*b2;
	coef_ab = a1*b2 + a2*b1;
	coef_a = a1*c2 + a2*c1;
	coef_b = b1*c2 + b2*c1;
	coef_c = c1*c2;

	if (coef_a2<-1) eq += coef_a2+"a^2"; else if (coef_a2===-1) eq += "-a^2"; else if (coef_a2===1) eq += "a^2"; else if (coef_a2>1) eq += coef_a2+"a^2";
	if (coef_b2<-1) eq += coef_b2+"b^2"; else if (coef_b2===-1) eq += "-b^2"; else if (coef_b2===1) eq += "+b^2"; else if (coef_b2>1) eq += "+"+coef_b2+"b^2";
	if (coef_ab<-1) eq += coef_ab+"ab"; else if (coef_ab===-1) eq += "-ab"; else if (coef_ab===1) eq += "+ab"; else if (coef_ab>1) eq += "+"+coef_ab+"ab";
	if (coef_a<-1) eq += coef_a+"a"; else if (coef_a===-1) eq += "-a"; else if (coef_a===1) eq += "+a"; else if (coef_a>1) eq += "+"+coef_a+"a";
	if (coef_b<-1) eq += coef_b+"b"; else if (coef_b===-1) eq += "-b"; else if (coef_b===1) eq += "+b"; else if (coef_b>1) eq += "+"+coef_b+"b";
	if (coef_c<-1) eq += coef_c; else if (coef_c===-1) eq += "-1"; else if (coef_c===1) eq += "+1"; else if (coef_c>1) eq += "+"+coef_c;
	eq += "=";

	ans_a1[0] = a1; ans_b1[0] = b1; ans_c1[0] = c1; ans_a2[0] = a2; ans_b2[0] = b2; ans_c2[0] = c2;
	ans_a1[1] = a1; ans_b1[1] = -b1; ans_c1[1] = -c1; ans_a2[1] = a2; ans_b2[1] = b2; ans_c2[1] = c2;
	ans_a1[2] = a1; ans_b1[2] = b1; ans_c1[2] = c1; ans_a2[2] = a2; ans_b2[2] = -b2; ans_c2[2] = -c2;
	ans_a1[3] = a1; ans_b1[3] = -b1; ans_c1[3] = c1; ans_a2[3] = a2; ans_b2[3] = b2; ans_c2[3] = -c2;

	for (let i=0; i<answers.length; i++) {
		answers[i] = "(";
		if (ans_a1[i]<-1) answers[i] += ans_a1[i]+"a"; else if (ans_a1[i]===-1) answers[i] += "-a"; else if (ans_a1[i]===1) answers[i] += "a"; else if (ans_a1[i]>1) answers[i] += ans_a1[i]+"a";
		if (ans_b1[i]<-1) answers[i] += ans_b1[i]+"b"; else if (ans_b1[i]===-1) answers[i] += "-b"; else if (ans_b1[i]===1) answers[i] += "+b"; else if (ans_b1[i]>1) answers[i] += "+"+ans_b1[i]+"b";
		if (ans_c1[i]<-1) answers[i] += ans_c1[i]+")("; else if (ans_c1[i]===-1) answers[i] += "-1)("; else if (ans_c1[i]===1) answers[i] += "+1)("; else if (ans_c1[i]>1) answers[i] += "+"+ans_c1[i]+")(";
		if (ans_a2[i]<-1) answers[i] += ans_a2[i]+"a"; else if (ans_a2[i]===-1) answers[i] += "-a"; else if (ans_a2[i]===1) answers[i] += "a"; else if (ans_a2[i]>1) answers[i] += ans_a2[i]+"a";
		if (ans_b2[i]<-1) answers[i] += ans_b2[i]+"b"; else if (ans_b2[i]===-1) answers[i] += "-b"; else if (ans_b2[i]===1) answers[i] += "+b"; else if (ans_b2[i]>1) answers[i] += "+"+ans_b2[i]+"b";
		if (ans_c2[i]<-1) answers[i] += ans_c2[i]+")"; else if (ans_c2[i]===-1) answers[i] += "-1)"; else if (ans_c2[i]===1) answers[i] += "+1)"; else if (ans_c2[i]>1) answers[i] += "+"+ans_c2[i]+")";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType2 = {
	generate,
};
export default MathQuestionGeneratorType2;
export { MathQuestionGeneratorType2 };
