import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let percent_w = 0;
	let percent_a = 0;
	let ans = [];

	do{
		if (random(2)===0) {
			percent_a = 100+(random(10)+1)*10;
		} else {
			percent_a = 100-(random(5)+1)*10;
		}

		if (random(2)===0) {
			percent_w = 100+(random(20)+1)*5;
		} else {
			percent_w = 100-(random(10)+1)*5;
		}

		if (language===ENGLISH) eq = "\\text{Area of a rectangle }"; else if (language===CHINESE) eq = "\\text{某長方形的面積 }";
		if (percent_a>100) eq += "↑"; else if (percent_a<100) eq += "↓";
		eq += Math.abs(percent_a-100)+"\\%";
		if (language===ENGLISH) eq += "\\\\\\text{when its width }"; else if (language===CHINESE) eq += "\\\\\\text{是因為它的闊度 }";
		if (percent_w>100) eq += "↑"; else if (percent_w<100) eq += "↓";
		eq += Math.abs(percent_w-100)+"\\%";
		if (language===ENGLISH) eq += "\\text{ and its height}"; else if (language===CHINESE) eq += "\\text{ 及它的高度}";

		ans[0] = Math.trunc((percent_a)/(percent_w)*100-100);
		ans[1] = percent_a-percent_w;
		ans[2] = percent_a+percent_w-200;
		ans[3] = Math.trunc(percent_a*percent_w/100-100);
	} while (ans[0]===ans[1] || ans[0]===ans[2] || ans[0]===ans[3] || ans[1]===ans[2] || ans[1]===ans[3] || ans[2]===ans[3]);

	answers[0] = "";
	if (ans[0]>0) answers[0] += "↑"+Math.abs(ans[0])+"\\%"; else if (ans[0]<0) answers[0] += "↓"+Math.abs(ans[0])+"\\%"; else if (ans[0]===0) answers[0] += language===CHINESE ? "\\text{維持不變}" : "\\text{remains unchanged}";

	answers[1] = "";
	if (ans[1]>0) answers[1] += "↑"+Math.abs(ans[1])+"\\%"; else if (ans[1]<0) answers[1] += "↓"+Math.abs(ans[1])+"\\%"; else if (ans[1]===0) answers[1] += language===CHINESE ? "\\text{維持不變}" : "\\text{remains unchanged}";

	answers[2] = "";
	if (ans[2]>0) answers[2] += "↑"+Math.abs(ans[2])+"\\%"; else if (ans[2]<0) answers[2] += "↓"+Math.abs(ans[2])+"\\%"; else if (ans[2]===0) answers[2] += language===CHINESE ? "\\text{維持不變}" : "\\text{remains unchanged}";

	answers[3] = "";
	if (ans[3]>0) answers[3] += "↑"+Math.abs(ans[3])+"\\%"; else if (ans[3]<0) answers[3] += "↓"+Math.abs(ans[3])+"\\%"; else if (ans[3]===0) answers[3] += language===CHINESE ? "\\text{維持不變}" : "\\text{remains unchanged}";

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType4 = {
	generate,
};
export default MathQuestionGeneratorType4;
export { MathQuestionGeneratorType4 };
