import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let a = 0;
	let i = 0;
	let term_n = 0;
	let term1 = 0;
	let term2 = 0;
	let ans = [];

	a = random(40)+1; if (random(2)===0) a = -a;
	i = random(10)+1; if (random(2)===0) i = -i;
	term1 = random(5)+1;
	term2 = random(5)+6;
	do {
		term_n = random(10)+1;
	} while (term_n===term1 || term_n===term2);

	if (language===ENGLISH) {
		eq = "\\text{The }"+term1+"^{";
		if (term1===1) eq += "st"; else if (term1===2) eq += "nd"; else if (term1===3) eq += "rd"; else if (term1>3) eq += "th";
		eq += "}\\text{ term of an AS is }"+(a+(term1-1)*i)+"\\text{.}\\\\\\text{The }"+term2+"^{";
		if (term2===1) eq += "st"; else if (term2===2) eq += "nd"; else if (term2===3) eq += "rd"; else if (term2>3) eq += "th";
		eq += "}\\text{ term is }"+(a+(term2-1)*i)+"\\text{.}\\\\\\text{Then the }"+term_n+"^{";
		if (term_n===1) eq += "st"; else if (term_n===2) eq += "nd"; else if (term_n===3) eq += "rd"; else if (term_n>3) eq += "th";
		eq += "}\\text{ term is}";
	} else if (language===CHINESE) {
		eq = "\\text{某等差數列的第 "+term1+" 項為 }"+(a+(term1-1)*i)+"\\text{。}\\\\\\text{第 "+term2+" 項為 }"+(a+(term2-1)*i)+"\\text{。}\\\\\\text{那第 "+term_n+" 項應為}";
	}

	ans[0] = a+i*(term_n-1);
	
	do {
		ans[1] = ans[0]; if (random(2)===0) ans[1] += random(10)+1; else ans[1] -= random(10)+1;
	} while (ans[1]===ans[0] || ans[1]===a+(term1-1)*i || ans[1]===a+(term2-1)*i);

	do {
		ans[2] = ans[1]; if (random(2)===0) ans[2] += random(10)+1; else ans[2] -= random(10)+1;
	} while (ans[2]===ans[0] || ans[2]===ans[1] || ans[2]===a+(term1-1)*i || ans[2]===a+(term2-1)*i);

	do {
		ans[3] = ans[2]; if (random(2)===0) ans[3] += random(10)+1; else ans[3] -= random(10)+1;
	} while (ans[3]===ans[0] || ans[3]===ans[1] || ans[3]===ans[2] || ans[3]===a+(term1-1)*i || ans[3]===a+(term2-1)*i);

	for (let j=0; j<answers.length; j++) {
		answers[j] = ""+ans[j];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType9 = {
	generate,
};
export default MathQuestionGeneratorType9;
export { MathQuestionGeneratorType9 };
