import { random } from "../random";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let c = 0;
	let coef1_x1 = 0,
		coef1_x0 = 0,
		coef2_x1 = 0,
		coef2_x0 = 0;
	let plus = true;
	let ans_x1 = [];
	let ans_x0 = [];

	do {
		c = random(3)+1; if (random(2)===0) c = -c;
		coef1_x1 = random(4)+1; if (random(2)===0) coef1_x1 = -coef1_x1;
		coef1_x0 = random(7)+1; if (random(2)===0) coef1_x0 = -coef1_x0;
		coef2_x1 = random(4)+1; if (random(2)===0) coef2_x1 = -coef2_x1;
		coef2_x0 = random(7)+1; if (random(2)===0) coef2_x0 = -coef2_x0;
	} while ((coef1_x1)/(coef2_x1)===(coef1_x0)/(coef2_x0));

	if (random(2)===0) plus = false;

	eq = "\\frac{"+c+"}{"+(-coef1_x0);
	if (-coef1_x1<-1) eq += (-coef1_x1)+"x"; else if (-coef1_x1===-1) eq += "-x"; else if (-coef1_x1===1) eq += "+x"; else if (-coef1_x1>1) eq += "+"+(-coef1_x1)+"x";
	eq += "}";
	if (plus) eq += "+"; else eq += "-";
	eq += "\\frac{";
	if (coef2_x1<-1) eq += coef2_x1+"x"; else if (coef2_x1===-1) eq += "-x"; else if (coef2_x1===1) eq += "x"; else if (coef2_x1>1) eq += coef2_x1+"x";
	if (coef2_x0<-1) eq += coef2_x0; else if (coef2_x0===-1) eq += "-1"; else if (coef2_x0===1) eq += "+1"; else if (coef2_x0>1) eq += "+"+coef2_x0;
	eq += "}{(";
	if (coef1_x1<-1) eq += coef1_x1+"x"; else if (coef1_x1===-1) eq += "-x"; else if (coef1_x1===1) eq += "x"; else if (coef1_x1>1) eq += coef1_x1+"x";
	if (coef1_x0<-1) eq += coef1_x0; else if (coef1_x0===-1) eq += "-1"; else if (coef1_x0===1) eq += "+1"; else if (coef1_x0>1) eq += "+"+coef1_x0;
	eq += ")^2}=";

	ans_x1[0] = -c*coef1_x1; if (plus) ans_x1[0] += coef2_x1; else ans_x1[0] -= coef2_x1;
	ans_x0[0] = -c*coef1_x0; if (plus) ans_x0[0] += coef2_x0; else ans_x0[0] -= coef2_x0;

	ans_x1[1] = -ans_x1[0];
	ans_x0[1] = ans_x0[0];

	ans_x1[2] = ans_x1[0];
	ans_x0[2] = -ans_x0[0];

	ans_x1[3] = -ans_x1[0];
	ans_x0[3] = -ans_x0[0];

	for (let i=0; i<answers.length; i++) {
		answers[i] = "\\frac{";
		if (ans_x1[i]<-1) answers[i] += ans_x1[i]+"x"; else if (ans_x1[i]===-1) answers[i] += "-x"; else if (ans_x1[i]===1) answers[i] += "x"; else if (ans_x1[i]>1) answers[i] += ans_x1[i]+"x";
		if (ans_x0[i]<-1) answers[i] += ans_x0[i]; else if (ans_x0[i]===-1) answers[i] += "-1"; else if (ans_x0[i]===1) answers[i] += "+1"; else if (ans_x0[i]>1) answers[i] += "+"+ans_x0[i];
		answers[i] += "}{(";
		if (coef1_x1<-1) answers[i] += coef1_x1+"x"; else if (coef1_x1===-1) answers[i] += "-x"; else if (coef1_x1===1) answers[i] += "x"; else if (coef1_x1>1) answers[i] += coef1_x1+"x";
		if (coef1_x0<-1) answers[i] += coef1_x0; else if (coef1_x0===-1) answers[i] += "-1"; else if (coef1_x0===1) answers[i] += "+1"; else if (coef1_x0>1) answers[i] += "+"+coef1_x0;
		answers[i] += ")^2}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType6 = {
	generate,
};
export default MathQuestionGeneratorType6;
export { MathQuestionGeneratorType6 };
