import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let m, f, m_t, f_t;
	let ans = [];

	m = random(9)+10;
	do {f = random(9)+10;} while (f===m || m+f>30);
	m_t = random(4)+1;
	do{f_t = random(4)+1;} while (f_t===m_t);

	if (language===ENGLISH) {
		eq = "\\text{In a class, there are }"+m+"\\text{ boys and }"+f+"\\text{ girls.}\\\\\\text{If }"+m_t+"\\text{ boys and }"+f_t+"\\text{ girls form a team,}\\\\\\text{how many different teams can be formed?}";
	} else if (language===CHINESE) {
		eq = "\\text{某班上有 }"+m+"\\text{ 男生和 }"+f+"\\text{ 女生。}\\\\\\text{若 }"+m_t+"\\text{ 男和 }"+f_t+"\\text{ 女能組成一隊，}\\\\\\text{問可組成多少不同的隊伍？}";
	}

	ans[0] = 1;
	for (let i=m; i>m-m_t; i--) ans[0] *= i; for (let i=1; i<=m_t; i++) ans[0] /= i;
	for (let i=f; i>f-f_t; i--) ans[0] *= i; for (let i=1; i<=f_t; i++) ans[0] /= i;

	ans[1] = 1;
	for (let i=m; i>m-f_t; i--) ans[1] *= i; for (let i=1; i<=f_t; i++) ans[1] /= i;
	for (let i=f; i>f-m_t; i--) ans[1] *= i; for (let i=1; i<=m_t; i++) ans[1] /= i;

	ans[2] = 1;
	for (let i=m+f; i>m+f-m_t-f_t; i--) ans[2] *= i; for (let i=1; i<=m_t+f_t; i++) ans[2] /= i;

	ans[3] = 1;
	for (let i=m; i>m-m_t; i--) ans[3] *= i;
	for (let i=f; i>f-f_t; i--) ans[3] *= i;

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType19 = {
	generate,
};
export default MathQuestionGeneratorType19;
export { MathQuestionGeneratorType19 };
