import { random, randomFloat } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let length = [];
	let ans = [];

	do {
		length[0] = random(20)+6;
		length[1] = random(20)+6;
		length[2] = random(20)+6;
	} while (length[0]===length[1] || length[0]===length[2] || length[1]===length[2]);

	if (language===ENGLISH) {
		eq = "\\text{If the measurements of sides of a triangle,}\\\\"+length[0]+"\\text{ cm, }"+length[1]+"\\text{ cm and }"+length[2]+"\\text{ cm, are correct to the nearest cm, the percentage error in calculating the perimeter of triangle}=";
	} else if (language===CHINESE) {
		eq = "\\text{某三角形的邊長量度至最近的 cm 為 }\\\\"+length[0]+"\\text{ cm、}"+length[1]+"\\text{ cm 及 }"+length[2]+"\\text{ cm，周界誤差的百分比}=";
	}

	ans[0] = 1.5/(length[0]+length[1]+length[2])*100;
	ans[1] = 2.7/(length[0]+length[1]+length[2])*100;
	do {
		ans[2] = (random(5)+1+randomFloat())/(length[0]+length[1]+length[2])*100;
	} while (ans[2]===ans[1]);
	do {
		ans[3] = (random(5)+1+randomFloat())/(length[0]+length[1]+length[2])*100;
	} while (ans[3]===ans[1] || ans[3]===ans[2]);

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i].toFixed(2)+"\\%";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType17 = {
	generate,
};
export default MathQuestionGeneratorType17;
export { MathQuestionGeneratorType17 };
