import { random } from "../random";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let x, y, a, b, c;
	let ans_x = [];
	let ans_y = [];
	let ans_a = [];
	let ans_b = [];
	let ans_c = [];
	let sign = [];

	x = random(5)+1; if (random(2)===0) x = -x;
	y = random(5)+1; if (random(2)===0) y = -y;
	do {
		a = random(5)+1; if (random(2)===0) a = -a;
		b = random(5)+1; if (random(2)===0) b = -b;
		c = random(5)+1; if (random(2)===0) c = -c;
	} while (Math.abs(a)===Math.abs(b) || Math.abs(b)===Math.abs(c) || Math.abs(a)===Math.abs(c));

	eq = "";
	if (x*a<-1) eq += x*a+"xa"; else if (x*a===-1) eq += "-xa"; else if (x*a===1) eq += "xa"; if (x*a>1) eq += x*a+"xa";
	if (y*a<-1) eq += y*a+"ya"; else if (y*a===-1) eq += "-ya"; else if (y*a===1) eq += "+ya"; if (y*a>1) eq += "+"+y*a+"ya";
	if (x*b<-1) eq += x*b+"xb"; else if (x*b===-1) eq += "-xb"; else if (x*b===1) eq += "+xb"; if (x*b>1) eq += "+"+x*b+"xb";
	if (y*b<-1) eq += y*b+"yb"; else if (y*b===-1) eq += "-yb"; else if (y*b===1) eq += "+yb"; if (y*b>1) eq += "+"+y*b+"yb";
	if (x*c<-1) eq += x*c+"xc"; else if (x*c===-1) eq += "-xc"; else if (x*c===1) eq += "+xc"; if (x*c>1) eq += "+"+x*c+"xc";
	if (y*c<-1) eq += y*c+"yc"; else if (y*c===-1) eq += "-yc"; else if (y*c===1) eq += "+yc"; if (y*c>1) eq += "+"+y*c+"yc";
	eq += "=";

	ans_x[0] = x; ans_y[0] = y; ans_a[0] = a; ans_b[0] = b; ans_c[0] = c;
	ans_x[1] = x; ans_y[1] = y; ans_a[1] = a; ans_b[1] = -b; ans_c[1] = -c;
	ans_x[2] = x; ans_y[2] = -y; ans_a[2] = a; ans_b[2] = b; ans_c[2] = c;
	ans_x[3] = x; ans_y[3] = -y; ans_a[3] = a; ans_b[3] = -b; ans_c[3] = -c;

	for (let i=0; i<4; i++) {
		sign[i] = true;
		if (ans_x[i]<0 && ans_y[i]<0) {
			sign[i] = !sign[i];
			ans_x[i] = -ans_x[i];
			ans_y[i] = -ans_y[i];
		}
		if (ans_a[i]<0 && ans_b[i]<0 && ans_c[i]<0) {
			sign[i] = !sign[i];
			ans_a[i] = -ans_a[i];
			ans_b[i] = -ans_b[i];
			ans_c[i] = -ans_c[i];
		}
	}

	for (let i=0; i<answers.length; i++) {
		if (sign[i]) answers[i] = ""; else answers[i] = "-";
		answers[i] += "(";
		if (ans_x[i]<-1) answers[i] += ans_x[i]+"x"; else if (ans_x[i]===-1) answers[i] += "-x"; else if (ans_x[i]===1) answers[i] += "x"; if (ans_x[i]>1) answers[i] += ans_x[i]+"x";
		if (ans_y[i]<-1) answers[i] += ans_y[i]+"y"; else if (ans_y[i]===-1) answers[i] += "-y"; else if (ans_y[i]===1) answers[i] += "+y"; if (ans_y[i]>1) answers[i] += "+"+ans_y[i]+"y";
		answers[i] += ")(";
		if (ans_a[i]<-1) answers[i] += ans_a[i]+"a"; else if (ans_a[i]===-1) answers[i] += "-a"; else if (ans_a[i]===1) answers[i] += "a"; if (ans_a[i]>1) answers[i] += ans_a[i]+"a";
		if (ans_b[i]<-1) answers[i] += ans_b[i]+"b"; else if (ans_b[i]===-1) answers[i] += "-b"; else if (ans_b[i]===1) answers[i] += "+b"; if (ans_b[i]>1) answers[i] += "+"+ans_b[i]+"b";
		if (ans_c[i]<-1) answers[i] += ans_c[i]+"c"; else if (ans_c[i]===-1) answers[i] += "-c"; else if (ans_c[i]===1) answers[i] += "+c"; if (ans_c[i]>1) answers[i] += "+"+ans_c[i]+"c";
		answers[i] += ")";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType25 = {
	generate,
};
export default MathQuestionGeneratorType25;
export { MathQuestionGeneratorType25 };
