import { memo, useState, useEffect, createRef } from "react";
import { Row, Col } from "react-bootstrap";
import "./index.css";
import katex from "katex";
import "katex/dist/katex.min.css";
import allExamQuestions from "../../config/allExamQuestions.json";

const ANSWERS_PER_ROW = 2;

function MathQuestion(props) {
	const {
		questionIndex,
		questionTypeNumber,
		equationLatex = "",
		answers = [],
		isMultipleChoice = false,
		isSubmitted = false,
	} = props;

	const refQ = createRef(null);
	const refAs = answers.map((e) => createRef());

	const [selectedAnswers, setSelectedAnswers] = useState([]);

	useEffect(() => {
		setSelectedAnswers([]);

		[
			{
				ref: refQ,
				eq: equationLatex,
				additionalParams: {
					fleqn: true,
				},
			},
			...refAs.map((e, i) => ({
				ref: refAs[i],
				eq: answers[i].text,
			})),
		].forEach((v) =>
			katex.render(v.eq, v.ref.current, {
				displayMode: true,
				strict: false,
				throwOnError: false,
				...v.additionalParams,
			})
		);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [equationLatex, answers]);

	return (
		<div className="math-question">
			<div
				className={[
					"question-number",
					...(questionIndex % 2 === 0 ? ["question-number-left"] : []),
					...(questionIndex % 2 === 1 ? ["question-number-right"] : []),
				].join(" ")}
			>
				{questionIndex + 1}
			</div>

			<Row className="math-q-row">
				<div className="math-q" ref={refQ}></div>
			</Row>

			<>
				{[...Array(Math.ceil(answers.length / ANSWERS_PER_ROW)).keys()].map((e) => {
					return (
						<Row key={e}>
							{[...Array(ANSWERS_PER_ROW).keys()].map((o) => {
								const index = e * ANSWERS_PER_ROW + o;

								return (
									<Col
										key={index}
										className={[
											"math-a-col",
											...(selectedAnswers.includes(index) ? ["selected"] : []),
											...(isSubmitted ? ["locked"] : []),
											...(isSubmitted &&
											selectedAnswers.includes(index) &&
											answers[index].isCorrect
												? ["selected-correct"]
												: []),
											...(isSubmitted &&
											!selectedAnswers.includes(index) &&
											answers[index].isCorrect
												? ["unselected-correct"]
												: []),
										].join(" ")}
										onClick={() => {
											if (!isSubmitted) {
												setSelectedAnswers((state) =>
													state.includes(index)
														? state.filter((s) => s !== index)
														: isMultipleChoice
														? [...state, index]
														: [index]
												);
											}
										}}
									>
										<div className="math-a" ref={refAs[index]}></div>
									</Col>
								);
							})}
						</Row>
					);
				})}
			</>

			<Row>
				<Col className="source-col">
					{allExamQuestions[`type${questionTypeNumber}`].sources
						.map((e) => `${e.year} #${e.questionNumber}`)
						.join(", ")}
				</Col>
			</Row>
		</div>
	);
}

const memoComponent = memo(
	MathQuestion,
	(prev, next) => prev.equationLatex === next.equationLatex && prev.isSubmitted === next.isSubmitted
);
export default memoComponent;
export { memoComponent as MathQuestion };
