import { random, randomFloat } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let investment = 0;
	let rate = 0;
	let period = 0;
	let temp = 0;
	let ans = [];

	investment = (random(5)+6)*500;
	rate = random(7)+1;
	period = (random(2)+1)*6;

	if (language===ENGLISH) {
		eq = "\\$M\\text{ is invested at the beginning of each month in ";
		if (period===6) eq += "half a year"; else if (period===12) eq += "a year";
		eq += " at an interest rate of "+rate+"\\% per annum, compounded monthly.}\\\\\\text{If it becomes \\$"+investment+" at the end, }M=";
	} else if (language===CHINESE) {
		eq = "\\text{在每月的首日，有 }\\$M\\text{ 資金被投資，}\\\\\\text{為期";
		if (period===6) eq += "半年"; else if (period===12) eq += "一年";
		eq += "，而年息為 "+rate+"\\%。}\\\\\\text{假如資金到最後變成  \\$"+investment+"，}M=";
	}

	temp = 0;
	for (let i=period; i>=1; i--) {
		temp += Math.pow(1+rate/100/12, i);
	}
	ans[0] = investment/temp;
	do {
		ans[1] = Math.trunc(ans[0]); if (random(2)===0) ans[1] += random(50)+21; else ans[1] -= random(50)+21; ans[1] += randomFloat();
	} while (Math.trunc(ans[1])===Math.trunc(ans[0]));
	do {
		ans[2] = Math.trunc(ans[1]); if (random(2)===0) ans[2] += random(50)+21; else ans[2] -= random(50)+21; ans[2] += randomFloat();
	} while ((Math.trunc(ans[2])===Math.trunc(ans[0])) || (Math.trunc(ans[2])===Math.trunc(ans[1])));
	do {
		ans[3] = Math.trunc(ans[2]); if (random(2)===0) ans[3] += random(50)+21; else ans[3] -= random(50)+21; ans[3] += randomFloat();
	} while (Math.trunc(ans[3])===Math.trunc(ans[0]) || Math.trunc(ans[3])===Math.trunc(ans[1]) || Math.trunc(ans[3])===Math.trunc(ans[2]));

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i].toFixed(2);
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType10 = {
	generate,
};
export default MathQuestionGeneratorType10;
export { MathQuestionGeneratorType10 };
