import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let xx, x, k, sum, product, q_type;
	let ans = [];

	q_type = random(4);
	do {
		xx = random(7)+1; if (random(2)===0) xx = -xx;
		sum = random(8)+1; if (random(2)===0) sum = -sum;
		x = -xx*sum;
		product = random(7)+1; if (random(2)===0) product = -product;
		k = xx*product;
	} while ((xx===x && x===k && xx===k) || ((q_type===1 || q_type===3) && (Math.pow(sum, 2)-4*product<=0 || (Math.sqrt(Math.pow(sum, 2)-4*product)%1!==0))));

	eq = "\\text{";
	if (language===ENGLISH) eq += "If"; else if (language===CHINESE) eq += "若";
	eq += " }α≠β\\text{ ";
	if (language===ENGLISH) eq += "and"; else if (language===CHINESE) eq += "而";
	eq += " }\\begin{cases}";
	if (xx<-1) eq += xx+"α^2"; else if (xx===-1) eq += "-α^2"; else if (xx===1) eq += "α^2"; else if (xx>1) eq += xx+"α^2";
	if (x<-1) eq += x+"α"; else if (x===-1) eq += "-α"; else if (x===1) eq += "+α"; else if (x>1) eq += "+"+x+"α";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	eq += "=0\\\\";
	if (xx<-1) eq += xx+"β^2"; else if (xx===-1) eq += "-β^2"; else if (xx===1) eq += "β^2"; else if (xx>1) eq += xx+"β^2";
	if (x<-1) eq += x+"β"; else if (x===-1) eq += "-β"; else if (x===1) eq += "+β"; else if (x>1) eq += "+"+x+"β";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	eq += "=0\\end{cases}\\text{";
	if (language===ENGLISH) eq += ", then"; else if (language===CHINESE) eq += "，那";
	eq += " }";
	switch (q_type) {
	case 0:
		eq += "α^2+β^2";
		break;
	case 1:
		eq += "α^2-β^2";
		break;
	case 2:
		eq += "α^3+β^3";
		break;
	case 3:
		eq += "α^3-β^3";
		break;
	default:
		break;
	}
	eq += "=";

	switch (q_type) {
	case 0:
		ans[0] = Math.pow(sum, 2)-2*product;
		break;
	case 1:
		ans[0] = Math.trunc(sum*Math.sqrt(Math.pow(sum, 2)-4*product));
		break;
	case 2:
		ans[0] = Math.trunc(sum*(Math.pow(sum, 2)-3*product));
		break;
	case 3:
		ans[0] = Math.trunc(Math.sqrt(Math.pow(sum, 2)-4*product)*(Math.pow(sum, 2)-product));
		break;
	default:
		break;
	}
	do {
		ans[1] = ans[0]; if (random(2)===0) ans[1] += random(30)+1; else ans[1] -= random(30)+1;
	} while (Math.abs(ans[1])===Math.abs(ans[0]));
	do {
		ans[2] = ans[1]; if (random(2)===0) ans[2] += random(30)+1; else ans[2] -= random(30)+1;
	} while (Math.abs(ans[2])===Math.abs(ans[0]) || Math.abs(ans[2])===Math.abs(ans[1]));
	do {
		ans[3] = ans[2]; if (random(2)===0) ans[3] += random(30)+1; else ans[3] -= random(30)+1;
	} while (Math.abs(ans[3])===Math.abs(ans[0]) || Math.abs(ans[3])===Math.abs(ans[1]) || Math.abs(ans[3])===Math.abs(ans[2]));

	for (let i=0; i<answers.length; i++) {
		if (ans[i]===0 || q_type===0 || q_type===2) {
			answers[i] = ""+ans[i];
		} else if (q_type===1 || q_type===3) {
			answers[i] = Math.abs(ans[i])+"\\text{ or }-"+Math.abs(ans[i]);
		}
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType34 = {
	generate,
};
export default MathQuestionGeneratorType34;
export { MathQuestionGeneratorType34 };
