import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let x = 0;
	let y = 0;
	let clockwise = true;
	let degree = 0;
	let clockwise_degree;
	let ans_x = [];
	let ans_y = [];

	do {
		x = random(8)+1;
		y = random(8)+1;
	} while (x===y);

	if (random(2)===0) x = -x;
	if (random(2)===0) y = -y;

	degree = (random(3)+1)*90;
	clockwise = random(2)===0;

	if (language===ENGLISH) {
		eq = "\\text{Point }("+x+", "+y+")\\text{ is rotated ";
		if (clockwise) eq += "clockwise"; else eq += "anti-clockwise";
		eq += " about the origin through "+degree+"°. Its new coordinates}=";
	} else if (language===CHINESE) {
		eq = "\\text{點 }("+x+", "+y+")\\text{ 圍繞原點";
		if (clockwise) eq += "順時針"; else eq += "逆時針";
		eq += "}\\\\\\text{"+degree+"° 轉。它的新坐標}=";
	}

	if (clockwise) {
		clockwise_degree = degree;
	} else {
		clockwise_degree = 360-degree;
	}

	if (clockwise_degree%360===90) {
		ans_x[0] = y; ans_y[0] = -x;
		ans_x[1] = x; ans_y[1] = -y;
		ans_x[2] = -y; ans_y[2] = x;
		ans_x[3] = -x; ans_y[3] = y;
	} else if (clockwise_degree%360===180) {
		ans_x[0] = -x; ans_y[0] = -y;
		ans_x[1] = -y; ans_y[1] = -x;
		ans_x[2] = x; ans_y[2] = -y;
		ans_x[3] = -x; ans_y[3] = y;
	} else if (clockwise_degree%360===270) {
		ans_x[0] = -y; ans_y[0] = x;
		ans_x[1] = -x; ans_y[1] = y;
		ans_x[2] = y; ans_y[2] = -x;
		ans_x[3] = x; ans_y[3] = -y;
	}

	for (let i=0; i<answers.length; i++) {
		answers[i] = "("+ans_x[i]+", "+ans_y[i]+")";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType5 = {
	generate,
};
export default MathQuestionGeneratorType5;
export { MathQuestionGeneratorType5 };
