import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let coef = ["",""];
	let x = [];
	let y = [];
	let z = [];
	let hcf_coef, hcf_x, hcf_y, hcf_z;
	let lcm_coef, lcm_x, lcm_y, lcm_z;
	let ans_coef = [];
	let ans_x = [];
	let ans_y = [];
	let ans_z = [];
	let check = [];

	do {
		for (let i=0; i<3; i++) {
			do {
				coef[i] = random(9)+1;
				x[i] = random(10);
				y[i] = random(10);
				z[i] = random(10);
			} while (x[i]+y[i]+z[i]<7);
		}
		for (let i=0; i<check.length; i++) check[i] = true;
		if (coef[0]===coef[1] || coef[1]===coef[2]) check[0] = false; if (!((coef[2]<coef[0] && coef[2]<coef[1]) || (coef[2]>coef[0] && coef[2]>coef[1]))) check[0] = false;
		if (x[0]===x[1] || x[1]===x[2]) check[1] = false; if (!((x[2]<x[0] && x[2]<x[1]) || (x[2]>x[0] && x[2]>x[1]))) check[1] = false;
		if (y[0]===y[1] || y[1]===y[2]) check[2] = false; if (!((y[2]<y[0] && y[2]<y[1]) || (y[2]>y[0] && y[2]>y[1]))) check[2] = false;
		if (z[0]===z[1] || z[1]===z[2]) check[3] = false; if (!((z[2]<z[0] && z[2]<z[1]) || (z[2]>z[0] && z[2]>z[1]))) check[3] = false;
	} while (!check[0] || !check[1] || !check[2] || !check[3]);

	hcf_coef = 1;
	for (let n=10; n>=2; n--) {
		if (coef[0]%n===0 && coef[1]%n===0 && coef[2]%n===0) {
			hcf_coef *= n;
		}
	}

	hcf_x = x[0];
	for (let i=1; i<x.length; i++) {
		if (x[i]<hcf_x) {
			hcf_x = x[i];
		}
	}

	hcf_y = y[0];
	for (let i=1; i<y.length; i++) {
		if (y[i]<hcf_y) {
			hcf_y = y[i];
		}
	}

	hcf_z = z[0];
	for (let i=1; i<z.length; i++) {
		if (z[i]<hcf_z) {
			hcf_z = z[i];
		}
	}

	lcm_coef = 0;
	for (let i=1; i<=coef[0]*coef[1]*coef[2]; i++) {
		if (i%coef[0]===0 && i%coef[1]===0 && i%coef[2]===0) {
			lcm_coef = i;
			break;
		}
	}

	lcm_x = x[0];
	for (let i=1; i<x.length; i++) {
		if (x[i]>lcm_x) {
			lcm_x = x[i];
		}
	}

	lcm_y = y[0];
	for (let i=1; i<y.length; i++) {
		if (y[i]>lcm_y) {
			lcm_y = y[i];
		}
	}

	lcm_z = z[0];
	for (let i=1; i<z.length; i++) {
		if (z[i]>lcm_z) {
			lcm_z = z[i];
		}
	}

	ans_coef[0] = coef[2]; ans_x[0] = x[2]; ans_y[0] = y[2]; ans_z[0] = z[2];

	if (coef[2]===lcm_coef) ans_coef[1] = hcf_coef; else ans_coef[1] = lcm_coef;
	if (x[2]===lcm_x) ans_x[1] = hcf_x; else ans_x[1] = lcm_x;
	if (y[2]===lcm_y) ans_y[1] = hcf_y; else ans_y[1] = lcm_y;
	if (z[2]===lcm_z) ans_z[1] = hcf_z; else ans_z[1] = lcm_z;

	if (coef[2]===lcm_coef) ans_coef[2] = hcf_coef; else ans_coef[2] = lcm_coef;
	if (x[2]===lcm_x) ans_x[2] = hcf_x; else ans_x[2] = lcm_x;
	if (y[2]===lcm_y) ans_y[2] = lcm_y; else ans_y[2] = hcf_y;
	if (z[2]===lcm_z) ans_z[2] = lcm_z; else ans_z[2] = hcf_z;

	if (coef[2]===lcm_coef) ans_coef[3] = lcm_coef; else ans_coef[3] = hcf_coef;
	if (x[2]===lcm_x) ans_x[3] = lcm_x; else ans_x[3] = hcf_x;
	if (y[2]===lcm_y) ans_y[3] = hcf_y; else ans_y[3] = lcm_y;
	if (z[2]===lcm_z) ans_z[3] = hcf_z; else ans_z[3] = lcm_z;

	if (language===ENGLISH) eq = "\\text{HCF and LCM of three expressions are}\\\\"; else if (language===CHINESE) eq = "\\text{三條代數表達式的最大公因數及最小公倍數為 }\\\\";
	if (hcf_coef>1) eq += hcf_coef;
	if (hcf_x===1) eq += "x"; else if (hcf_x>1) eq += "x^{"+hcf_x+"}";
	if (hcf_y===1) eq += "y"; else if (hcf_y>1) eq += "y^{"+hcf_y+"}";
	if (hcf_z===1) eq += "z"; else if (hcf_z>1) eq += "z^{"+hcf_z+"}";
	if (language===ENGLISH) eq += "\\text{ and }"; else if (language===CHINESE) eq += "\\text{ 及 }";
	if (lcm_coef>1) eq += lcm_coef;
	if (lcm_x===1) eq += "x"; else if (lcm_x>1) eq += "x^{"+lcm_x+"}";
	if (lcm_y===1) eq += "y"; else if (lcm_y>1) eq += "y^{"+lcm_y+"}";
	if (lcm_z===1) eq += "z"; else if (lcm_z>1) eq += "z^{"+lcm_z+"}";
	if (language===ENGLISH) eq += "\\text{. If }1^{st}\\text{ and }2^{nd}\\text{ expressions are }"; else if (language===CHINESE) eq += "\\text{。如果其中兩條式為 }";
	if (coef[0]>1) eq += coef[0];
	if (x[0]===1) eq += "x"; else if (x[0]>1) eq += "x^{"+x[0]+"}";
	if (y[0]===1) eq += "y"; else if (y[0]>1) eq += "y^{"+y[0]+"}";
	if (z[0]===1) eq += "z"; else if (z[0]>1) eq += "z^{"+z[0]+"}";
	if (language===ENGLISH) eq += "\\text{ and }"; else if (language===CHINESE) eq += "\\text{ 及 }";
	if (coef[1]>1) eq += coef[1];
	if (x[1]===1) eq += "x"; else if (x[1]>1) eq += "x^{"+x[1]+"}";
	if (y[1]===1) eq += "y"; else if (y[1]>1) eq += "y^{"+y[1]+"}";
	if (z[1]===1) eq += "z"; else if (z[1]>1) eq += "z^{"+z[1]+"}";
	if (language===ENGLISH) eq += "\\text{, }3^{rd}\\text{ expression is}"; else if (language===CHINESE) eq += "\\text{，那第三條式為}";

	for (let i=0; i<answers.length; i++) {
		answers[i] = "";
		if (ans_coef[i]>1) answers[i] += ans_coef[i];
		if (ans_x[i]===1) answers[i] += "x"; else if (ans_x[i]>1) answers[i] += "x^{"+ans_x[i]+"}";
		if (ans_y[i]===1) answers[i] += "y"; else if (ans_y[i]>1) answers[i] += "y^{"+ans_y[i]+"}";
		if (ans_z[i]===1) answers[i] += "z"; else if (ans_z[i]>1) answers[i] += "z^{"+ans_z[i]+"}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType32 = {
	generate,
};
export default MathQuestionGeneratorType32;
export { MathQuestionGeneratorType32 };
