import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let x, y;
	let ans_x = [];
	let ans_y = [];
	let ans_z = [];

	x = random(5)+1; if (random(2)===0) x = -x;
	y = random(5)+1; if (random(2)===0) y = -y;

	ans_x[0] = x; ans_y[0] = y; ans_z[0] = -1;
	ans_x[1] = x; ans_y[1] = y; ans_z[1] = 1;
	ans_x[2] = -x; ans_y[2] = y; ans_z[2] = -1;
	ans_x[3] = x; ans_y[3] = -y; ans_z[3] = -1;

	if (language===ENGLISH) {
		eq = "\\text{If }z\\text{ varies ";
		if (x>0) eq += "directly"; else if (x<0) eq += "inversely";
		eq += " as }x";
		if (Math.abs(x)>1) eq += "^"+Math.abs(x);
		eq += "\\text{ and ";
		if (y>0) eq += "directly"; else if (y<0) eq += "inversely";
		eq += " as }y";
		if (Math.abs(y)>1) eq += "^"+Math.abs(y);
		eq += "\\text{, which of the followings must be a constant:}";
	} else if (language===CHINESE) {
		eq = "\\text{若 }z\\text{ 隨 }x";
		if (Math.abs(x)>1) eq += "^"+Math.abs(x);
		eq += "\\text{";
		if (x>0) eq += " 正變"; else if (x<0) eq += " 反變";
		eq += "}\\text{及隨 }y";
		if (Math.abs(y)>1) eq += "^"+Math.abs(y);
		eq += "\\text{";
		if (y>0) eq += " 正變"; else if (y<0) eq += " 反變";
		eq += "，}\\\\\\text{以下哪個必為常數：}";
	}

	for (let i=0; i<answers.length; i++) {
		answers[i] = "";
		if (ans_x[i]>0 && ans_y[i]>0 && ans_z[i]>0) {
			answers[i] += "x"; if (ans_x[i]>1) answers[i] += "^"+ans_x[i];
			answers[i] += "y"; if (ans_y[i]>1) answers[i] += "^"+ans_y[i];
			answers[i] += "z"; if (ans_z[i]>1) answers[i] += "^"+ans_z[i];
		} else if (ans_x[i]<0 && ans_y[i]<0 && ans_z[i]<0) {
			answers[i] += "\\frac{1}{";
			answers[i] += "x"; if (ans_x[i]<-1) answers[i] += "^"+(-ans_x[i]);
			answers[i] += "y"; if (ans_y[i]<-1) answers[i] += "^"+(-ans_y[i]);
			answers[i] += "z"; if (ans_z[i]<-1) answers[i] += "^"+(-ans_z[i]);
			answers[i] += "}";
		} else {
			answers[i] += "\\frac{";
			if (ans_x[i]>0) {answers[i] += "x"; if (ans_x[i]>1) answers[i] += "^"+ans_x[i];}
			if (ans_y[i]>0) {answers[i] += "y"; if (ans_y[i]>1) answers[i] += "^"+ans_y[i];}
			if (ans_z[i]>0) {answers[i] += "z"; if (ans_z[i]>1) answers[i] += "^"+ans_z[i];}
			answers[i] += "}{";
			if (ans_x[i]<0) {answers[i] += "x"; if (ans_x[i]<-1) answers[i] += "^"+(-ans_x[i]);}
			if (ans_y[i]<0) {answers[i] += "y"; if (ans_y[i]<-1) answers[i] += "^"+(-ans_y[i]);}
			if (ans_z[i]<0) {answers[i] += "z"; if (ans_z[i]<-1) answers[i] += "^"+(-ans_z[i]);}
			answers[i] += "}";
		}
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType16 = {
	generate,
};
export default MathQuestionGeneratorType16;
export { MathQuestionGeneratorType16 };
