import { random } from "../random";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let a_n_coef, a_n_2_coef, a_d_coef, a_d_2_coef;
	let b_n_coef, b_n_2_coef, b_d_coef, b_d_2_coef;
	let index_n, index_d;
	let ans_a = [];
	let ans_b = [];

	if (random(2)===0) a_n_coef = random(3)+2; else a_n_coef = 0;
	if (a_n_coef===0 || random(2)===0) b_n_coef = random(3)+2; else b_n_coef = 0;
	if (random(2)===0) a_n_2_coef = random(3)+2; else a_n_2_coef = 0;
	if (a_n_2_coef===0 || random(2)===0) b_n_2_coef = random(3)+2; else b_n_2_coef = 0;
	index_n = random(3)+2;

	do {
		if (random(2)===0) a_d_coef = random(3)+2; else a_d_coef = 0;
		if (a_d_coef===0 || random(2)===0) b_d_coef = random(3)+2; else b_d_coef = 0;
		if (random(2)===0) a_d_2_coef = random(3)+2; else a_d_2_coef = 0;
		if (a_d_2_coef===0 || random(2)===0) b_d_2_coef = random(3)+2; else b_d_2_coef = 0;
		index_d = random(3)+2;
	} while ((a_n_coef+a_n_2_coef*index_n===a_d_coef+a_d_2_coef*index_d && b_n_coef+b_n_2_coef*index_n===b_d_coef+b_d_2_coef*index_d) || a_n_coef===a_d_coef || b_n_coef===b_d_coef || a_n_2_coef===a_d_2_coef || b_n_2_coef===b_d_2_coef || index_n===index_d);

	eq += "\\frac{";
	if (a_n_coef>0) eq += "a^"+a_n_coef;
	if (b_n_coef>0) eq += "b^"+b_n_coef;
	eq += "(";
	if (a_n_2_coef>0) eq += "a^"+a_n_2_coef;
	if (b_n_2_coef>0) eq += "b^"+b_n_2_coef;
	eq += ")^"+index_n+"}{";
	if (a_d_coef>0) eq += "a^"+a_d_coef;
	if (b_d_coef>0) eq += "b^"+b_d_coef;
	eq += "(";
	if (a_d_2_coef>0) eq += "a^"+a_d_2_coef;
	if (b_d_2_coef>0) eq += "b^"+b_d_2_coef;
	eq += ")^"+index_d+"}=";

	ans_a[0] = a_n_coef+a_n_2_coef*index_n-a_d_coef-a_d_2_coef*index_d;
	ans_b[0] = b_n_coef+b_n_2_coef*index_n-b_d_coef-b_d_2_coef*index_d;

	do {
		ans_a[1] = random(41)-20;
		ans_b[1] = random(41)-20;
	} while ((ans_a[1]===0 && ans_b[1]===0) || (ans_a[1]===ans_a[0] && ans_b[1]===ans_b[0]));
	do {
		ans_a[2] = ans_a[0];
		ans_b[2] = random(41)-20;
	} while ((ans_a[2]===0 && ans_b[2]===0) || (ans_a[2]===ans_a[0] && ans_b[2]===ans_b[0]) || (ans_a[2]===ans_a[1] && ans_b[2]===ans_b[1]));
	do {
		ans_a[3] = random(41)-20;
		ans_b[3] = ans_b[0];
	} while ((ans_a[3]===0 && ans_b[3]===0) || (ans_a[3]===ans_a[0] && ans_b[3]===ans_b[0]) || (ans_a[3]===ans_a[1] && ans_b[3]===ans_b[1]) || (ans_a[3]===ans_a[2] && ans_b[3]===ans_b[2]));

	for (let i=0; i<answers.length; i++) {
		if (ans_a[i]>=0 && ans_b[i]>=0) {
			answers[i] = "";
			if (ans_a[i]===1) answers[i] += "a"; else if (ans_a[i]>1) answers[i] += "a^{"+ans_a[i]+"}";
			if (ans_b[i]===1) answers[i] += "b"; else if (ans_b[i]>1) answers[i] += "b^{"+ans_b[i]+"}";
		} else if (ans_a[i]<=0 && ans_b[i]<=0) {
			answers[i] = "\\frac{1}{";
			if (ans_a[i]===-1) answers[i] += "a"; else if (ans_a[i]<-1) answers[i] += "a^{"+(-ans_a[i])+"}";
			if (ans_b[i]===-1) answers[i] += "b"; else if (ans_b[i]<-1) answers[i] += "b^{"+(-ans_b[i])+"}";
			answers[i] += "}";
		} else if ((ans_a[i]>0 && ans_b[i]<0) || (ans_a[i]<0 && ans_b[i]>0)) {
			answers[i] = "\\frac{";
			if (ans_a[i]===1) answers[i] += "a"; else if (ans_a[i]>1) answers[i] += "a^{"+ans_a[i]+"}";
			else if (ans_b[i]===1) answers[i] += "b"; else if (ans_b[i]>1) answers[i] += "b^{"+ans_b[i]+"}";
			answers[i] += "}{";
			if (ans_a[i]===-1) answers[i] += "a"; else if (ans_a[i]<-1) answers[i] += "a^{"+(-ans_a[i])+"}";
			if (ans_b[i]===-1) answers[i] += "b"; else if (ans_b[i]<-1) answers[i] += "b^{"+(-ans_b[i])+"}";
			answers[i] += "}";
		}
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType1 = {
	generate,
};
export default MathQuestionGeneratorType1;
export { MathQuestionGeneratorType1 };
