import "./App.css";
import { HashRouter, Switch, Route, Redirect } from "react-router-dom";
import { ExamPage } from "./components/pages";
import { SiteFooter } from "./components/SiteFooter";

function App() {
	return (
		<div style={{ padding: "1em 0" }}>
			<HashRouter>
				<Switch>
					<Route exact sensitive path="/:lang(tc|en)" component={ExamPage} />
					<Redirect to="/tc" />
				</Switch>
			</HashRouter>

			<SiteFooter />
		</div>
	);
}

export default App;
export { App };
