import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let x1, x2, y1, y2, mid_x, mid_y;
	let ans_x = [];
	let ans_y = [];
	let ans_k = [];

	do {
		x1 = random(10); if (random(2)===0) x1 = -x1;
		y1 = random(10); if (random(2)===0) y1 = -y1;
		x2 = random(10); if (random(2)===0) x2 = -x2;
		y2 = random(10); if (random(2)===0) y2 = -y2;
	} while ((x1===x2 && y1===y2) || (x1+x2)%2!==0 || (y1+y2)%2!==0);

	if (language===ENGLISH) {
		eq = "\\text{Points }A\\text{, }B\\text{ are at }("+x1+","+y1+")\\text{ and }("+x2+","+y2+")\\text{.}\\\\\\text{Let }P\\text{ be a moving point in the rectangular coordinate plane such that }AP=BP\\text{.}\\\\\\text{The equation of locus of }P\\text{ is:}";
	} else if (language===CHINESE) {
		eq = "\\text{點 }A\\text{、}B\\text{ 的直角座標為 }("+x1+","+y1+")\\text{ 和 }("+x2+","+y2+")\\text{。}\\\\\\text{設 }P\\text{ 為一個移動點使得 }AP=BP\\text{。}\\\\P\\text{ 的軌跡方程式為：}";
	}

	mid_x = (x1+x2)/2;
	mid_y = (y1+y2)/2;

	ans_x[0] = x1-x2; ans_y[0] = y1-y2; ans_k[0] = mid_y*(y2-y1)-mid_x*(x1-x2);
	do {
		ans_x[1] = random(6); if (random(2)===0) ans_x[1] = -ans_x[1];
		ans_y[1] = random(6); if (random(2)===0) ans_y[1] = -ans_y[1];
		ans_k[1] = random(16); if (random(2)===0) ans_k[1] = -ans_k[1];
	} while ((ans_x[1]===0 && ans_y[1]===0) || (ans_x[1]===ans_x[0] && ans_y[1]===ans_y[0] && ans_k[1]===ans_k[0]));
	do {
		ans_x[2] = random(6); if (random(2)===0) ans_x[2] = -ans_x[2];
		ans_y[2] = random(6); if (random(2)===0) ans_y[2] = -ans_y[2];
		ans_k[2] = random(16); if (random(2)===0) ans_k[2] = -ans_k[2];
	} while ((ans_x[2]===0 && ans_y[2]===0) || (ans_x[2]===ans_x[0] && ans_y[2]===ans_y[0] && ans_k[2]===ans_k[0]) || (ans_x[2]===ans_x[1] && ans_y[2]===ans_y[1] && ans_k[2]===ans_k[1]));
	do {
		ans_x[3] = random(6); if (random(2)===0) ans_x[3] = -ans_x[3];
		ans_y[3] = random(6); if (random(2)===0) ans_y[3] = -ans_y[3];
		ans_k[3] = random(16); if (random(2)===0) ans_k[3] = -ans_k[3];
	} while ((ans_x[3]===0 && ans_y[3]===0) || (ans_x[3]===ans_x[0] && ans_y[3]===ans_y[0] && ans_k[3]===ans_k[0]) || (ans_x[3]===ans_x[1] && ans_y[3]===ans_y[1] && ans_k[3]===ans_k[1]) || (ans_x[3]===ans_x[2] && ans_y[3]===ans_y[2] && ans_k[3]===ans_k[2]));

	for (let i=0; i<4; i++) {
		if (ans_x[i]===0) {
			if (ans_y[i]<0) {
				ans_y[i] = -ans_y[i];
				ans_k[i] = -ans_k[i];
			}
		} else if (ans_x[i]<0) {
			ans_x[i] = -ans_x[i];
			ans_y[i] = -ans_y[i];
			ans_k[i] = -ans_k[i];
		}
		for (let n=10; n>=2; n--) {
			if (ans_x[i]%n===0 && ans_y[i]%n===0 && ans_k[i]%n===0) {
				ans_x[i] /= n; ans_y[i] /= n; ans_k[i] /= n;
			}
		}
	}

	for (let i=0; i<answers.length; i++) {
		answers[i] = "";
		if (ans_x[i]<-1) answers[i] += ans_x[i]+"x"; else if (ans_x[i]===-1) answers[i] += "-x"; else if (ans_x[i]===1) answers[i] += "x"; else if (ans_x[i]>1) answers[i] += ans_x[i]+"x";
		if (ans_x[i]!==0 && ans_y[i]>=1) answers[i] += "+";
		if (ans_y[i]<-1) answers[i] += ans_y[i]+"y"; else if (ans_y[i]===-1) answers[i] += "-y"; else if (ans_y[i]===1) answers[i] += "y"; else if (ans_y[i]>1) answers[i] += ans_y[i]+"y";
		if (ans_k[i]<-1) answers[i] += ans_k[i]; else if (ans_k[i]===-1) answers[i] += "-1"; else if (ans_k[i]===1) answers[i] += "+1"; else if (ans_k[i]>1) answers[i] += "+"+ans_k[i];
		answers[i] += "=0";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType30 = {
	generate,
};
export default MathQuestionGeneratorType30;
export { MathQuestionGeneratorType30 };
