import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let people, type;
	let event = null;
	let position = null;
	let ans = [];

	if (language===CHINESE) {
		event = ["足球協會", "角色扮演", "小學學校", "電影製作"];
		position = [["會長", "秘書", "財務"], ["警長", "消防員", "醫生"], ["領袖", "風紀", "班長"], ["導演", "製片人", "演員"]];
	} else {
		event = ["football club", "role play", "primary school", "movie making"];
		position = [["chairman", "secretary", "treasurer"], ["policeman", "fireman", "doctor"], ["leader", "prefect", "helper"], ["director", "producer", "actor"]];
	}

	people = random(14)+7;
	type = random(4);

	if (language===ENGLISH) eq = "\\text{Including Roy and Kay, }"+people+"\\text{ people are in a "+event[type]+".}\\\\\\text{Select three for "+position[type][0]+", "+position[type][1]+" and "+position[type][2]+".}\\\\\\text{Roy and Kay cannot be both selected.}\\\\\\text{Number of combinations}=";
	else if (language===CHINESE) eq = "\\text{包括羅伊和凱兒，}"+people+"\\text{ 人加入了"+event[type]+"。}\\\\\\text{當中三人擔任"+position[type][0]+"、"+position[type][1]+"及"+position[type][2]+"。}\\\\\\text{羅伊和凱兒不可以同時任職。}\\\\\\text{問最多能出現多少個組合？}";

	let temp1 = 1; for (let i=people-2; i>people-2-3; i--) temp1 *= i;
	let temp2 = 1; for (let i=people-2; i>people-2-2; i--) temp2 *= i; for (let i=1; i<=2; i++) temp2 /= i;

	ans[0] = temp1+2*temp2*(3*2*1);
	do {
		if (random(2)===0) ans[1] = temp1+temp2; else ans[1] = 2*temp1*(3*2*1)+temp2;
	} while (ans[1]===ans[0]);
	do {
		if (random(2)===0) ans[2] = temp2*(3*2*1); else ans[2] = 2*temp1+temp2*(3*2*1);
	} while (ans[2]===ans[0] || ans[2]===ans[1]);
	do {
		if (random(2)===0) ans[3] = 2*temp2*(3*2*1); else ans[3] = 2*(temp1+temp2*(3*2*1));
	} while (ans[3]===ans[0] || ans[3]===ans[1] || ans[3]===ans[2]);

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType33 = {
	generate,
};
export default MathQuestionGeneratorType33;
export { MathQuestionGeneratorType33 };
