import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let x1, x2, a1, a2, bx, c, k;
	let ans_a = [];
	let ans_b = [];
	let ans_c = [];

	do {
		x1 = random(5)+1; if (random(2)===0) x1 = -x1;
		x2 = random(5)+1; if (random(2)===0) x2 = -x2;
		a1 = random(5)+1; if (random(2)===0) a1 = -a1;
		a2 = random(5)+1; if (random(2)===0) a2 = -a2;
		bx = random(5)+1; if (random(2)===0) bx = -bx;
		c = random(5)+1; if (random(2)===0) c = -c;
		k = random(5)+1; if (random(2)===0) k = -k;
	} while (a1*x1===k*bx && a2===k*c);

	if (language===ENGLISH) eq = "\\text{If }a\\text{, }b\\text{, }c\\text{ are non-zero constants such that }\\\\"; else if (language===CHINESE) eq = "\\text{若 }a\\text{、}b\\text{、}c\\text{ 為非零常數使得 }\\\\";
	if (x1<-1) eq += x1+"x"; else if (x1===-1) eq += "-x"; else if (x1===1) eq += "x"; else if (x1>1) eq += x1+"x";
	eq += "(";
	if (x2<-1) eq += x2+"x"; else if (x2===-1) eq += "-x"; else if (x2===1) eq += "x"; else if (x2>1) eq += x2+"x";
	if (a1<-1) eq += a1+"a"; else if (a1===-1) eq += "-a"; else if (a1===1) eq += "+a"; else if (a1>1) eq += "+"+a1+"a";
	eq += ")";
	if (a2<-1) eq += a2+"a"; else if (a2===-1) eq += "-a"; else if (a2===1) eq += "+a"; else if (a2>1) eq += "+"+a2+"a";
	eq += "{\\equiv}";
	if (x1*x2<-1) eq += x1*x2+"x^2"; else if (x1*x2===-1) eq += "-x^2"; else if (x1*x2===1) eq += "x^2"; else if (x1*x2>1) eq += x1*x2+"x^2";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	eq += "(";
	if (bx<-1) eq += bx+"bx"; else if (bx===-1) eq += "-bx"; else if (bx===1) eq += "bx"; else if (bx>1) eq += bx+"bx";
	if (c<-1) eq += c+"c"; else if (c===-1) eq += "-c"; else if (c===1) eq += "+c"; else if (c>1) eq += "+"+c+"c";
	if (language===ENGLISH) eq += ")\\text{.}\\\\\\text{Then }a:b:c="; else if (language===CHINESE) eq += ")\\text{。}\\\\\\text{那 }a:b:c=";

	if (k*bx===k*c) {
		ans_a[0] = k*bx; ans_b[0] = a1*x1; ans_c[0] = a2;
	} else if (k*bx>k*c) {
		if ((k*bx)%(k*c)===0) {
			ans_a[0] = k*bx; ans_b[0] = a1*x1; ans_c[0] = a2*(k*bx)/(k*c);
		} else {
			ans_a[0] = (k*bx)*(k*c); ans_b[0] = (a1*x1)*(k*c); ans_c[0] = a2*(k*bx);
		}
	} else if (k*bx<k*c) {
		if ((k*c)%(k*bx)===0) {
			ans_a[0] = k*c; ans_b[0] = (a1*x1)*(k*c)/(k*bx); ans_c[0] = a2;
		} else {
			ans_a[0] = (k*bx)*(k*c); ans_b[0] = (a1*x1)*(k*c); ans_c[0] = a2*(k*bx);
		}
	}

	let lcm = ans_a[0]*ans_b[0]*ans_c[0];
	ans_a[1] = lcm/ans_a[0]; ans_b[1] = lcm/ans_b[0]; ans_c[1] = lcm/ans_c[0];
	ans_a[2] = ans_a[0]; ans_b[2] = ans_c[0]; ans_c[2] = ans_b[0];
	while (ans_b[2]===ans_b[0] && ans_c[2]===ans_c[0]) {
		if (random(2)===0) {
			ans_b[2] = -ans_b[2];
		} else {
			ans_c[2] = -ans_c[2];
		}
	}
	ans_a[3] = ans_b[0]; ans_b[3] = ans_a[0]; ans_c[3] = ans_c[0];
	while (ans_a[3]===ans_a[0] && ans_b[3]===ans_b[0]) {
		if (random(2)===0) {
			ans_a[3] = -ans_a[3];
		} else {
			ans_b[3] = -ans_b[3];
		}
	}

	for (let i=0; i<4; i++) {
		if (ans_a[i]<0 && ans_b[i]<0 && ans_c[i]<0) {
			ans_a[i] = -ans_a[i]; ans_b[i] = -ans_b[i]; ans_c[i] = -ans_c[i];
		}
		for (let n=10; n>=2; n--) {
			if (ans_a[i]%n===0 && ans_b[i]%n===0 && ans_c[i]%n===0) {
				ans_a[i] /= n; ans_b[i] /= n; ans_c[i] /= n;
			}
		}
	}

	for (let i=0; i<answers.length; i++) {
		answers[i] = ans_a[i]+":"+ans_b[i]+":"+ans_c[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType24 = {
	generate,
};
export default MathQuestionGeneratorType24;
export { MathQuestionGeneratorType24 };
