import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let a1 = 0;
	let a2 = 0;
	let n = 0;
	let ans = [];

	a1 = random(5)+1; if (random(2)===0) a1 = -a1;
	a2 = random(5)+1; if (random(2)===0) a2 = -a2;
	n = random(7)+7;

	if (language===ENGLISH) {
		eq = "\\text{Let }a_{n}\\text{ be the }n\\text{th term of a sequence.}\\\\\\text{If }a_{1}="+a1+"\\text{, }a_{2}="+a2+"\\text{ and }a_{n+2}=a_{n}+a_{n+1}\\text{ for any positive integer }n\\text{, then }a_{"+n+"}=";
	} else if (language===CHINESE) {
		eq = "\\text{設 }a_{n}\\text{ 為某數列的第 }n\\text{ 項。}\\\\\\text{如 }a_{1}="+a1+"\\text{，}a_{2}="+a2+"\\text{ 及 }a_{n+2}=a_{n}+a_{n+1}\\\\\\text{而 }n\\text{ 為任何正整數，那 }a_{"+n+"}=";
	}

	let temp1 = a1, temp2 = a2, temp3 = 0;
	for (let i=3; i<=n; i++) {
		temp3 = temp1+temp2;
		temp1 = temp2;
		temp2 = temp3;
	}
	ans[0] = temp3;

	do {
		for (let  j=1; j<4; j++) {
			temp1 = a1; temp2 = a2; temp3 = 0;
			ans[j] = random(3)+1; if (random(2)===0) ans[j] = -ans[j]; ans[j] += n;
			for (let i=3; i<=ans[j]; i++) {
				temp3 = temp1+temp2;
				temp1 = temp2;
				temp2 = temp3;
			}
			ans[j] = temp3;
		}
	} while (ans[0]===ans[1] || ans[0]===ans[2] || ans[0]===ans[3] || ans[1]===ans[2] || ans[1]===ans[3] || ans[2]===ans[3]);

	for (let i=0; i<ans.length; i++) {
		answers[i] = ""+ans[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType13 = {
	generate,
};
export default MathQuestionGeneratorType13;
export { MathQuestionGeneratorType13 };
