import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let p, q, px, x1, c1, x_sq, qx, x2, x3, c2;
	let ans = [];

	do {
		p = random(5)+1; if (random(2)===0) p = -p;
		q = random(5)+1; if (random(2)===0) q = -q;
		px = random(10)+1; if (random(2)===0) px = -px;
		x1 = random(10)+1; if (random(2)===0) x1 = -x1;
		c1 = random(10)+1; if (random(2)===0) c1 = -c1;
		x_sq = random(10)+1; if (random(2)===0) x_sq = -x_sq;
		qx = random(10)+1; if (random(2)===0) qx = -qx;
		x2 = random(10)+1; if (random(2)===0) x2 = -x2;
		c2 = random(10)+1; if (random(2)===0) c2 = -c2;
		x3 = random(10)+1; if (random(2)===0) x3 = -x3;
	} while (!(px*x1*p+x_sq===qx*q*x2 && px*p*c1-x3===qx*q*c2 && px*c1*x2!==px*x1*c2));

	if (language===ENGLISH) eq = "\\text{If }p\\text{ and }q\\text{ are constants such that}\\\\"; else if (language===CHINESE) eq = "\\text{如 }p\\text{ 和 }q\\text{ 為常數使得}\\\\";
	if (px<-1) eq += px+"px"; else if (px===-1) eq += "-px"; else if (px===1) eq += "px"; else if (px>1) eq += px+"px";
	eq += "(";
	if (x1<-1) eq += x1+"x"; else if (x1===-1) eq += "-x"; else if (x1===1) eq += "x"; else if (x1>1) eq += x1+"x";
	if (c1<-1) eq += c1; else if (c1===-1) eq += "-1"; else if (c1===1) eq += "+1"; else if (c1>1) eq += "+"+c1;
	eq += ")";
	if (x_sq<-1) eq += x_sq+"x^2"; else if (x_sq===-1) eq += "-x^2"; else if (x_sq===1) eq += "+x^2"; else if (x_sq>1) eq += "+"+x_sq+"x^2";
	eq += "{\\equiv}";
	if (qx<-1) eq += qx+"qx"; else if (qx===-1) eq += "-qx"; else if (qx===1) eq += "qx"; else if (qx>1) eq += qx+"qx";
	eq += "(";
	if (x2<-1) eq += x2+"x"; else if (x2===-1) eq += "-x"; else if (x2===1) eq += "x"; else if (x2>1) eq += x2+"x";
	if (c2<-1) eq += c2; else if (c2===-1) eq += "-1"; else if (c2===1) eq += "+1"; else if (c2>1) eq += "+"+c2;
	eq += ")";
	if (x3<-1) eq += x3+"x"; else if (x3===-1) eq += "-x"; else if (x3===1) eq += "+x"; else if (x3>1) eq += "+"+x3+"x";
	if (language===ENGLISH) eq += "\\text{, then }p="; else if (language===CHINESE) eq += "\\text{，那}p=";

	ans[0] = p;
	do {
		ans[1] = ans[0]; if (random(2)===0) ans[1] += random(3)+1; else ans[1] -= random(3)+1;
	} while (ans[1]===ans[0] || ans[1]<-5 || ans[1]>5);
	do {
		ans[2] = ans[1]; if (random(2)===0) ans[2] += random(3)+1; else ans[2] -= random(3)+1;
	} while (ans[2]===ans[0] || ans[2]===ans[1] || ans[2]<-5 || ans[2]>5);
	do {
		ans[3] = ans[2]; if (random(2)===0) ans[3] += random(3)+1; else ans[3] -= random(3)+1;
	} while (ans[3]===ans[0] || ans[3]===ans[1] || ans[3]===ans[2] || ans[3]<-5 || ans[3]>5);

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType15 = {
	generate,
};
export default MathQuestionGeneratorType15;
export { MathQuestionGeneratorType15 };
