import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let k = 0;
	let ki = 0;
	let c = 0;
	let plus = true;
	let ans_k = [];
	let ans_i = [];

	k = random(7)+1;
	c = random(7)+1;
	ki = random(7)+1;
	if (random(2)===0) plus = false;

	if (language===ENGLISH) eq = "\\text{If }k\\text{ is a real number, then }"; else if (language===CHINESE) eq = "\\text{如 }k\\text{ 為實數，那 }";
	if (k<-1) eq += k+"k"; else if (k===-1) eq += "-k"; else if (k===1) eq += "k"; else if (k>1) eq += k+"k";
	if (plus) eq += "+"; else eq += "-";
	eq += "\\frac{"+c;
	if (ki<-1) eq += ki+"ki"; else if (ki===-1) eq += "-ki"; else if (ki===1) eq += "+ki"; else if (ki>1) eq += "+"+ki+"ki";
	eq += "}{i}=";

	
	ans_k[0] = k; if (plus) ans_k[0] += ki; else ans_k[0] -= ki;
	if (plus) ans_i[0] = -c; else ans_i[0] = c;

	ans_k[1] = ans_k[0];
	ans_i[1] = -ans_i[0];

	ans_k[2] = k; if (plus) ans_k[2] -= ki; else ans_k[2] += ki;
	ans_i[2] = -ans_i[0];

	ans_k[3] = ans_k[2];
	ans_i[3] = -ans_i[2];

	for (let i=0; i<answers.length; i++) {
		answers[i] = "";
		if (ans_k[i]<-1) answers[i] += ans_k[i]+"k"; else if (ans_k[i]===-1) answers[i] += "-k"; else if (ans_k[i]===1) answers[i] += "k"; else if (ans_k[i]>1) answers[i] += ans_k[i]+"k";
		if (ans_k[i]!==0 && ans_i[i]>=1) answers[i] += "+";
		if (ans_i[i]<-1) answers[i] += ans_i[i]+"i"; else if (ans_i[i]===-1) answers[i] += "-i"; else if (ans_i[i]===1) answers[i] += "i"; else if (ans_i[i]>1) answers[i] += ans_i[i]+"i";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType8 = {
	generate,
};
export default MathQuestionGeneratorType8;
export { MathQuestionGeneratorType8 };
