import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let variance, coef, k;
	let ans = [];

	variance = random(16)+10;
	coef = random(6)+2; if (random(2)===0) coef = -coef;
	k = random(10)+1; if (random(2)===0) k = -k;

	if (language===ENGLISH) {
		eq = "\\text{If the variance of }x_1\\text{, }x_2\\text{, }x_3\\text{, }x_4\\text{ and }x_5\\text{ is}\\\\"+variance+"\\text{, then the variance of }";
	} else if (language===CHINESE) {
		eq = "\\text{若 }x_1\\text{、}x_2\\text{、}x_3\\text{、}x_4\\text{ 及 }x_5\\text{ 的方差為 }"+variance+"\\text{，}\\\\\\text{那 }";
	}
	if (coef<-1) eq += coef+"x_1"; else if (coef===-1) eq += "-x_1"; else if (coef===1) eq += "x_1"; else if (coef>1) eq += coef+"x_1";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	if (language===ENGLISH) eq += "\\text{, }"; else if (language===CHINESE) eq += "\\text{、}";
	if (coef<-1) eq += coef+"x_2"; else if (coef===-1) eq += "-x_2"; else if (coef===1) eq += "x_2"; else if (coef>1) eq += coef+"x_2";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	if (language===ENGLISH) eq += "\\text{, }"; else if (language===CHINESE) eq += "\\text{、}";
	if (coef<-1) eq += coef+"x_3"; else if (coef===-1) eq += "-x_3"; else if (coef===1) eq += "x_3"; else if (coef>1) eq += coef+"x_3";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	if (language===ENGLISH) eq += "\\text{, }"; else if (language===CHINESE) eq += "\\text{、}";
	if (coef<-1) eq += coef+"x_4"; else if (coef===-1) eq += "-x_4"; else if (coef===1) eq += "x_4"; else if (coef>1) eq += coef+"x_4";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	if (language===ENGLISH) eq += "\\text{ and }"; else if (language===CHINESE) eq += "\\text{ 及 }";
	if (coef<-1) eq += coef+"x_5"; else if (coef===-1) eq += "-x_5"; else if (coef===1) eq += "x_5"; else if (coef>1) eq += coef+"x_5";
	if (k<-1) eq += k; else if (k===-1) eq += "-1"; else if (k===1) eq += "+1"; else if (k>1) eq += "+"+k;
	if (language===ENGLISH) eq += "\\text{ is}"; else if (language===CHINESE) eq += "\\text{ 的方差為}";

	ans[0] = Math.trunc(Math.pow(coef, 2)*variance);
	ans[1] = Math.trunc(Math.pow(coef, 2)*variance)+k;
	ans[2] = Math.abs(variance*coef);
	ans[3] = Math.abs(variance*coef+k);

	for (let i=0; i<answers.length; i++) {
		answers[i] = ""+ans[i];
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType28 = {
	generate,
};
export default MathQuestionGeneratorType28;
export { MathQuestionGeneratorType28 };
