import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let m, f;
	let ans_n = [];
	let ans_d = [];

	m = random(3)+2;
	f = random(6)+5;

	if (language===ENGLISH) {
		eq = "\\text{If }"+m+"\\text{ boys and }"+f+"\\text{ girls randomly form a queue, probability that all boys stand next to each others}=";
	} else if (language===CHINESE) {
		eq = "\\text{若 }"+m+"\\text{ 男和 }"+f+"\\text{ 女隨機組成一橫排，}\\\\\\text{男生站在一起的機會率}=";
	}

	ans_n[0] = 1; for (let i=1; i<=m; i++) ans_n[0] *= i;
	ans_d[0] = 1; for (let i=m+f; i>f+1; i--) ans_d[0] *= i;
	for (let n=20; n>=2; n--) {
		if (ans_n[0]%n===0 && ans_d[0]%n===0) {
			ans_n[0] /= n;
			ans_d[0] /= n;
		}
	}

	for (let n=20; n>=2; n--) {
		if (ans_n[0]%n===0 && ans_d[0]%n===0) {
			ans_n[0] /= n;
			ans_d[0] /= n;
		}
	}

	do {
		ans_n[1] = 1;
		ans_d[1] = ans_d[0];
		do {
			for (let j=1; j<=7; j++) {
				if (ans_n[1]*j<ans_d[1] && random(3)===0) {
					ans_n[1] *= j;
					break;
				}
			}
		} while (random(2)===0 || ans_n[1]===1);
		for (let n=20; n>=2; n--) {
			if (ans_n[1]%n===0 && ans_d[1]%n===0) {
				ans_n[1] /= n;
				ans_d[1] /= n;
			}
		}
	} while (ans_n[1]===ans_n[0] && ans_d[1]===ans_d[0]);

	do {
		ans_n[2] = 1;
		ans_d[2] = ans_d[0];
		do {
			for (let j=1; j<=7; j++) {
				if (ans_n[2]*j<ans_d[2] && random(3)===0) {
					ans_n[2] *= j;
					break;
				}
			}
		} while (random(2)===0 || ans_n[2]===1);
		for (let n=20; n>=2; n--) {
			if (ans_n[2]%n===0 && ans_d[2]%n===0) {
				ans_n[2] /= n;
				ans_d[2] /= n;
			}
		}
	} while ((ans_n[2]===ans_n[0] && ans_d[2]===ans_d[0]) || (ans_n[2]===ans_n[1] && ans_d[2]===ans_d[1]));

	do {
		ans_n[3] = 1;
		ans_d[3] = ans_d[0]*(random(2)+2);
		do {
			for (let j=1; j<=7; j++) {
				if (ans_n[3]*j<ans_d[3] && random(3)===0) {
					ans_n[3] *= j;
					break;
				}
			}
		} while (random(2)===0 || ans_n[3]===1);
		for (let n=20; n>=2; n--) {
			if (ans_n[3]%n===0 && ans_d[3]%n===0) {
				ans_n[3] /= n;
				ans_d[3] /= n;
			}
		}
	} while ((ans_n[3]===ans_n[0] && ans_d[3]===ans_d[0]) || (ans_n[3]===ans_n[1] && ans_d[3]===ans_d[1]) || (ans_n[3]===ans_n[2] && ans_d[3]===ans_d[2]));

	for (let i=0; i<answers.length; i++) {
		answers[i] = "\\frac{"+ans_n[i]+"}{"+ans_d[i]+"}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType20 = {
	generate,
};
export default MathQuestionGeneratorType20;
export { MathQuestionGeneratorType20 };
