import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let y1, y2, c1, c2, a, b;
	let coef1 = [];
	let coef2 = [];
	let coef3 = [];
	let coef4 = [];

	y1 = random(5)+1; if (random(2)===0) y1 = -y1;
	y2 = random(5)+1; if (random(2)===0) y2 = -y2;
	c1 = random(5)+1; if (random(2)===0) c1 = -c1;
	c2 = random(5)+1; if (random(2)===0) c2 = -c2;
	do {
		a = random(5)+1; if (random(2)===0) a = -a;
		b = random(5)+1; if (random(2)===0) b = -b;
	} while (Math.abs(a)===Math.abs(b));

	eq = "\\frac{";
	if (y1<-1) eq += y1+"y"; else if (y1===-1) eq += "-y"; else if (y1===1) eq += "y"; else if (y1>1) eq += y1+"y";
	if (c1<-1) eq += c1; else if (c1===-1) eq += "-1"; else if (c1===1) eq += "+1"; else if (c1>1) eq += "+"+c1;
	eq += "}{";
	if (a<-1) eq += a+"a"; else if (a===-1) eq += "-a"; else if (a===1) eq += "a"; else if (a>1) eq += a+"a";
	eq += "}=\\frac{";
	if (y2<-1) eq += y2+"y"; else if (y2===-1) eq += "-y"; else if (y2===1) eq += "y"; else if (y2>1) eq += y2+"y";
	if (c2<-1) eq += c2; else if (c2===-1) eq += "-1"; else if (c2===1) eq += "+1"; else if (c2>1) eq += "+"+c2;
	eq += "}{";
	if (b<-1) eq += b+"b"; else if (b===-1) eq += "-b"; else if (b===1) eq += "b"; else if (b>1) eq += b+"b";
	if (language===ENGLISH) eq += "}\\text{. Then }y="; else if (language===CHINESE) eq += "}\\text{。那 }y=";

	coef1[0] = c2*a; coef2[0] = -c1*b; coef3[0] = y1*b; coef4[0] = -y2*a;
	coef1[1] = c2*a; coef2[1] = c1*b; coef3[1] = y1*b; coef4[1] = -y2*a;
	coef1[2] = c2*a; coef2[2] = c1*b; coef3[2] = -y1*b; coef4[2] = y2*a;
	coef1[3] = c2*a; coef2[3] = -c1*b; coef3[3] = -y1*b; coef4[3] = y2*a;

	for (let i=0; i<4; i++) {
		if (coef1[i]<0 && coef2[i]<0 && coef3[i]<0 && coef4[i]<0) {
			coef1[i] = -coef1[i];
			coef2[i] = -coef2[i];
			coef3[i] = -coef3[i];
			coef4[i] = -coef4[i];
		}
		for (let n=7; n>=2; n--) {
			if (coef1[i]%n===0 && coef2[i]%n===0 && coef3[i]%n===0 && coef4[i]%n===0) {
				coef1[i] /= n;
				coef2[i] /= n;
				coef3[i] /= n;
				coef4[i] /= n;
			}
		}
	}

	for (let i=0; i<answers.length; i++) {
		answers[i] = "\\frac{";
		if (coef1[i]<-1) answers[i] += coef1[i]+"a"; else if (coef1[i]===-1) answers[i] += "-a"; else if (coef1[i]===1) answers[i] += "a"; else if (coef1[i]>1) answers[i] += coef1[i]+"a";
		if (coef2[i]<-1) answers[i] += coef2[i]+"b"; else if (coef2[i]===-1) answers[i] += "-b"; else if (coef2[i]===1) answers[i] += "+b"; else if (coef2[i]>1) answers[i] += "+"+coef2[i]+"b";
		answers[i] += "}{";
		if (coef4[i]<-1) answers[i] += coef4[i]+"a"; else if (coef4[i]===-1) answers[i] += "-a"; else if (coef4[i]===1) answers[i] += "a"; else if (coef4[i]>1) answers[i] += coef4[i]+"a";
		if (coef3[i]<-1) answers[i] += coef3[i]+"b"; else if (coef3[i]===-1) answers[i] += "-b"; else if (coef3[i]===1) answers[i] += "+b"; else if (coef3[i]>1) answers[i] += "+"+coef3[i]+"b";
		answers[i] += "}";
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType22 = {
	generate,
};
export default MathQuestionGeneratorType22;
export { MathQuestionGeneratorType22 };
